
.page404 {
  text-align: center;
  min-height: 60vh;
  padding: 50px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  h2 {
    font-size: 120px;
    margin-bottom: 30px;
    line-height: 1;
  }
  &__desc {
    margin-bottom: 20px;
  }
  a {
    text-decoration: none;
    margin: 0 3px;
    color: $pink;
    border-bottom: 1px solid $pink;
    transition: .3s ease;
    &:hover {
      opacity: 0.6;
    }
  }
  &__inner {
    height: 60vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    @media (max-width: 1080px) {
      min-height: auto;
    }
  }
}
