@import "include/bs-grid/grid";
@import 'utils/vars';
@import 'utils/mixins';
@import 'parts/navigation';


h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
}

ul, li {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
}

a {
  color: inherit;
}.logo img {
  display: block;
}

audio,
canvas,
iframe,
img,
svg,
video {
  vertical-align: middle;
  max-width: 100%;
}

textarea {
  resize: none;
}

section {
  position: relative;
}

input,
select,
button {
  outline: none;
}[type='submit']  {
  cursor: pointer;
}

*, *:before, *:after {
  box-sizing: border-box;
}

@media (min-width: 1101px) {
  a, [type=submit]  {

    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  }
}



/*site styles*/
html {
  font-size: $baseFontSize;
  min-height: 100%;
  line-height: 1.2;
  overflow-x: hidden;

}

body {
  font-family: $ff;
  background: #fff;
  color: $color;
  font-weight: normal;
  font-size: 16px;
  overflow-x: hidden;
  @media (max-width: 767px) {
    font-size: 14px;
  }
  &.hidden {
    overflow: hidden;
    max-height: 100vh;
  }
}

h2 {
  font-size: 24px;
  line-height: 36px;
  font-weight: 700;
}

h3 {
  font-size: 35px;
  line-height: 44px;
  @media (max-width: 767px) {
    font-size: 30px;
    line-height: 40px;
  }
}
h4 {
  font-size: 20px;
  line-height: 30px;
}
h5 {
  font-size: 35px;
  line-height: 44px;
  font-weight: 700;
}
h6 {
  font-size: 14px;
  line-height: 20px;
  font-weight: 700;
}
p {
  font-size: 16px;
  line-height: 20px;
}

strong {
  font-weight: bold;
  margin-left: 5px;
}

.page-title {
  text-transform: uppercase;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: calc(100vh);
}
.container {
  max-width: 1400px;
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
  &_sm {
    max-width: 850px;
  }
  &_md {
    max-width: 1034px;
  }
}
.page {
  @media (max-width: 1080px) {
    padding-top: 72px;
  }
}

.text {
  h4 {
    text-align: center;
  }
  ul {
    padding-left: 10px;
    margin-bottom: 40px;
    li {
      list-style-type: square;
    }
  }

  ol{
    padding-left: 10px;
    margin-bottom: 40px;
    li {
      list-style-type: decimal;
    }
  }
  p {
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 40px;
  }
  .left {
    float: left;
    margin-right: 10px;
    margin-bottom: 10px;
    @media (max-width: 1080px) {
      float: none;
      margin: 0 auto 40px;
    }
  }
  .right {
    float: right;
    margin-left: 10px;
    margin-bottom: 10px;
    @media (max-width: 1080px) {
      float: none;
      margin: 0 auto 40px;
    }
  }
}

.link {
  display: inline-block;
  transition: .3s;
  cursor: pointer;
  transition: opacity .3s ease;
  &:hover {
    text-decoration: none;
    opacity: 0.6;
  }

  &_pink {
    text-decoration: none;
    color: $pink;
    border-bottom: 1px solid $pink;
    padding-bottom: 3px;
    &:hover {
      opacity: 0.6;
    }
  }
}
.btn  {
  @include btn();
  &_white {
    background-color: #FFF;
    border-color: #FFF;
    color: $color;
    &:hover {
      background-color: transparent;
      color: #FFF;
      border-color: #FFF;
    }
  }
  &_pink {
    background-color: #EB818B;
    border-color: #EB818B;
    color: #fff;
    &:hover {
      background-color: transparent;
      color: #EB818B;
      border-color: #EB818B;
    }
  }
  &_transp {
    background-color: #FFF;
    border-color: $color;
    color: $color;
    &:hover {
      background-color: $color;
      color: #FFF;
    }
  }
  &_green {
    background-color: #FFF;
    border-color: #75BCB1;
    color: #75BCB1;
    &:hover {
      border-color: #75BCB1;
      background-color: #75BCB1;
      color: #FFF;
    }
  }
  @media (max-width: 480px) {
    padding: 5px 3px;
  }
}

.ov-h {
  -ms-overflow-y: hidden;
  overflow-y: hidden;
}
.ov-x-h {
  -ms-overflow-x: hidden;
  overflow-x: hidden;
}
.ov-a {
  -ms-overflow-y: auto;
  overflow-y: auto;
  height: 100%;
}

.ov-x-a {
  -ms-overflow-x: auto;
  overflow-x: auto;
  height: 100%;
}

.inner {
  display: flex;
  -webkit-justify-content: space-between;
  justify-content: space-between;
  flex-wrap: wrap;
}

.socials {
  display: flex;
  justify-content: flex-start;
  li{
    margin: 0 20px;
    &:first-child {
      margin-left: 0;
    }
    &:last-child {
      margin-right: 0;
    }
  }
  a{
    transition: all .3s ease;
    svg {
      transition: all .3s ease;
      width: 22px;
      height: 22px;
      display: block;
      fill: $color;
    }
  }
  a {
    &:hover {
      svg {
        fill: $pink;
      }
    }
  }

}

.note {
  span {
    text-transform: uppercase;
    font-size: 14px;
    line-height: 20px;
    font-weight: 500;
    position: relative;
    padding-bottom: 17px;
    display: inline-block;
    &:after {
      content: "";
      display: inline-block;
      position: absolute;
      width: 80%;
      left: 0;
      bottom: 0;
      height: 2px;
      background-color: $color;
    }
  }
}

.price {
  font-size: 20px;
  line-height: 24px;
  display: inline-block;
  vertical-align: middle;
  margin-right: 10px;
  &_prev {
    color: #9E9E9E;
    text-decoration: line-through;
    margin-right: 0;
  }
}
.discount  {
  background-color: #EB818B;
  padding: 5px 14px;
  color: #FFF;
  font-weight: 700;
  line-height: 20px;
  display: inline-block;
  max-width: 130px;
}
.wish-btn {
  svg {
    stroke: #9C9C9C;
    transition: all .3s ease;
  }
  &:hover {
    svg{
      fill: #9C9C9C;
      opacity: 0.6;
    }
  }
  &.active {
    svg {
      fill: #9C9C9C;
    }
  }
}

.visible-mob {
  display: none;
  @media (max-width: 767px) {
    display: block;
  }
}
.hidden-mob {
  @media (max-width: 767px) {
    display: none;
  }
}
// BREADCRUMBS
.breadcrumbs{
  margin-top: 20px;
  margin-bottom: 35px;
  text-align: left;
  &__item {
    margin-right: 15px;
    display: inline-block;
  }
    &:last-child {
      margin-right: 0;
    }
  a, span {
    display: inline-block;
    font-size: 12px;
  }
  a {
    position: relative;
    padding-right: 20px;
    text-decoration: none;
    transition: all .3s ease;
    &:after{
      content: "";
      display: block;
      width: 6px;
      height: 6px;
      position: absolute;
      top: 50%;
      transform: translateY(-50%) rotate(45deg);
      right: 0;
      border-top: 1px solid #474A53;
      border-right: 1px solid #474A53;
    }
    &:hover {
      color: $pink;
    }
  }
}

.tab-content {
  .tab-pane {
    display: none;
    &.active {
      display: block;
    }
  }
}

.t-a-right {
  text-align: right;
}
.t-a-center {
  text-align: center;
}
.show {
  .about__hero h3 {
    animation: up-anim 2s ease .1s;
  }
  .about__hero-text {
    animation: up-anim 2s ease .1s;
  }
}


@keyframes up-anim {
  0% {
    opacity: 0;
    transform: translateY(50%);
  }
  40% {
    opacity: 0;
    transform: translateY(50%);
  }
  70% {
    opacity: 1;
    transform: none;
  }
  100% {
    opacity: 1;
    transform: none;
  }
}




