
input::-webkit-input-placeholder {
  color: #9C9C9C;
}
input::-moz-placeholder {
  color: #9C9C9C;
}
input:-moz-placeholder {
  color: #9C9C9C;
}
input:-ms-input-placeholder {
  color: #9C9C9C;
}
textarea::-webkit-input-placeholder {
  color: #9C9C9C;
}
textarea::-moz-placeholder {
  color: #9C9C9C;
}
textarea:-moz-placeholder {
  color: #9C9C9C;
}
textarea:-ms-input-placeholder {
  color: #9C9C9C;
}


// hide close default btn in input type search
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  -webkit-appearance:none;
}

input:not([type="checkbox"]):not([type="radio"]), textarea {
  display: block;
  width: 100%;
}
textarea {
  resize: none;
  font-size: 14px;
}
.send-form, .webform-client-form {
  position: relative;
  font-size: 20px;
  color: $color;
  @media (max-width: 767px) {
    font-size: 14px;
  }
}
.form-group {
  margin-bottom: 2em;
  padding: 0;
  border: 0;&:after {
    content: '';
    display: table;
    clear: both;
  }
}

.form-item {
  position: relative;
  text-align: left;
  margin-bottom: 5px;
  padding-bottom: 15px;
  @media (max-width: 767px) {
    margin-bottom: 5px;
 }
 label {
   font-weight: normal;
 }
 .parsley-errors-list {
   margin-top: 5px;
   font-size: 10px;
   position: absolute;
   left: 0;
   top: 80%;
   color: #B94A48;
 }
 &.hidden {
   display: none;
 }
 &.promocode {
    padding-bottom: 0;
  .desc{
    font-size: 12px;
    color: #7D7D7D;
    margin-bottom: 8px;
  }
  input {
    margin-bottom: 8px;
  }
  button {
    background: none;
    border: none;
    color: #75BCB1;
    border-bottom: 1px solid #75BCB1;
    font-size: 12px;
    padding: 0;
    transition: .3s;
    &:hover {
      opacity: 0.6;
    }
  }
 }

 &_pass {
    .watch {
      cursor: pointer;
      position: absolute;
      right: 19px;
      bottom: 20px;
      z-index: 2;
      transition: .3s ease;
      &:hover {
        opacity: 0.6;
      }
    }
    input {
      padding: 9px 45px 9px 16px;
    }
    .link {
      position: absolute;
      font-size: 12px;
      line-height: 10px;
      top: 7px;
      right: 0;
    }
 }
  &_select {
    position: relative;
    .parsley-errors-list.filled {
      position: absolute;
      bottom: 0;
      left: 0;
      & + .select2-container--default .select2-selection--single {
        background-color: #F2DEDE;
      }
    }
  }

  &.error {
    label, .in-text {
      color: red;
    }
    span {
      opacity: 1;
    }
  }
  .error-text {
    opacity: 0;
    font-size: 10px;
    line-height: 1;
    position: absolute;
    left: 0;
    bottom: 0;
  }
  input, select {
    display: block;
    width: 100%;
    background: transparent;
    font-size: 14px;
    line-height: 18px;
    padding: 9px 16px;
    border: none;
    outline: none;
    border: 1px solid #ccc;
    &.parsley-error {
      background-color: #F2DEDE;
    }
  }
  select {
    -ms-appearance: none;
    appearance: none;
  }

  textarea {
    display: block;
    width: 100%;
    height: 80px;
    padding: 10px;
    background: #fff;
    border: none;
    outline: none;
    border: 1px solid #CCCCCC;
    &.parsley-error {
      background-color: #F2DEDE;
    }
  }

  label {
    font-size: 14px;
    font-weight: normal;
    transition: transform 0.2s ease-out;
    transform-origin: 0 100%;
    pointer-events: none;
    display: inline-block;
    margin-bottom: 8px;
    span {
      display: inline-block;
      opacity: 0;
      transition: opacity 0.5s;
    }
  }

  input, textarea, select {
    &:focus + label {
      transform: translateY(-1.6em) scale(0.85);
      transform-origin: 0 0;
    }
  }
  .in-text {
    position: relative;
    height: 3.2em;
    line-height: 3.2em;
    background: #fff;
    border-bottom: 1px solid #341209;& after {
      content: "\e913";
      font-family: 'font-icon' !important;
      position: absolute;
      font-size: 33px;
      bottom: 0px;
      right: 0;
      line-height: 1;
      transform: translateY(-50%);
      pointer-events: none;
    }

    span {
      font-weight: 500;
      text-transform: uppercase;
    }
    input {
      position: absolute;
      opacity: 0;
      top: 0;
      left: 0;
    }
  }
  &_textarea {
      textarea {
        border: 1px solid #CCCCCC;
        padding: 9px 16px;
      }
   }
  &_sertif {
    &.disabled {
     input {
       background-color: #EBEBEB;
     }
    }
 }
 &_sum {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    input {
      position: absolute;
      top: 0;
      left: 0;
      visibility: hidden;
      opacity: 0;
      pointer-events: none;
      &:checked + label {
        border-color: $pink;
      }
    }
    label {
      padding: 6px;
      margin-bottom: 0;
      font-size: 20px;
      line-height: 24px;
      border: 2px solid #EBEBEB;
      border-radius: 10px;
      min-height: 60px;
      min-width: 80px;
      width: 100%;
      text-align: center;
      cursor: pointer;
      pointer-events: auto;
      transition: boreder-color .3s ease;
      @media (max-width: 1200px) {
        font-size: 16px;
        line-height: 20px;
        min-width: 60px;
      }
      @media (max-width: 990px) {
        display: flex;
        justify-content: center;
        align-items: center;
      }
      @media (max-width: 480px) {
        font-size: 12px;
        line-height: 18px;
      }
      span {
        opacity: 1;
        margin-right: 3px;
        display: block;
      }
    }
 }
}

.fieldset{
  &__title {
    width: 100%;
    font-size: 14px;
    font-weight: normal;
    -webkit-transition: -webkit-transform 0.2s ease-out;
    transition: -webkit-transform 0.2s ease-out;
    -o-transition: transform 0.2s ease-out;
    transition: transform 0.2s ease-out;
    transition: transform 0.2s ease-out, -webkit-transform 0.2s ease-out;
    -webkit-transform-origin: 0 100%;
    -ms-transform-origin: 0 100%;
    transform-origin: 0 100%;
    pointer-events: none;
    display: inline-block;
    margin-bottom: 8px;
  }
  &_adress {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .form-item {
      width: calc(100% / 3 - 10px);
      @media (max-width: 767px) {
        width: calc(100% / 3 - 2px);
      }
    }
    &_m {
      justify-content: flex-start;
      .form-item {
        &:not(:last-child) {
          margin-right: 20px;
        }
        &:nth-child(2) {
          width: 20%;

        }
      }
    }
  }
  &_sum {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .form-item {
      width: calc(100% / 4 - 7px);

    }
  }
}

.form-check {
  input {
    position: absolute;
    top: 0;
    left: 0;
    visibility: hidden;
    opacity: 0;
    pointer-events: none;
    &:checked + label:after {
      visibility: visible;
    }
  }
  label {
    position: relative;
    display: flex;
    align-items: center;&:before {
      content: '';
      flex-shrink: 0;
      display: inline-block;
      margin-right: 8px;
      width: 24px;
      height: 24px;
      border: 1px solid #f93;
      border-radius: 3px;
    }&:after {
      content: '';
      position: absolute;
      top: 3px;
      left: 9px;
      width: 8px;
      height: 15px;
      border: 2px solid #f93;
      border-top: 0;
      border-left: 0;
      transform: rotate(35deg);
      visibility: hidden;
    }
  }
  &.error {
    label {
      color: red;
    }
  }
}
.form-action {
  position: relative;
  margin-top: 15px;
  [type='submit'], input[type='submit'] {
    // border: 0;
    outline: none;
    display: inline-block;
    // @include btn();
  }.ajax-progress {
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
  }
}.form-thank {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 10%;
  font-size: 2em;
  background: #fff;
  display: none;.wrap {
    width: 100%;
    height: 100%;
    border: 1px solid #333;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    align-content: center;
    justify-content: center;
    text-align: center;
  }

  h4 {
    width: 100%;
    text-transform: uppercase;
    font-weight: 300;
  }
}.send-form__item, .send-form__check {
  &.error {
    background: none;
  }
}


/* == media == */


.search {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background-color: #FFF;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  transition: all .3s ease;
  opacity: 0;
  pointer-events: none;
  &.active {
    opacity: 1;
    pointer-events: auto;
  }

  &_mob {
    display: none;
    @media (max-width: 1080px) {
      padding: 0 23px;
      display: flex;
      top: 70px;
      transform: translateY(-100%);
      position: absolute;
      width: 100%;
    }
  }
  &_desk {
    @media (max-width: 1080px) {
      display: none;
    }
  }
  .form-action {
    width: 10%;
    margin-top: 0;
    button {
      background-color: transparent;
      border: none;
      outline: none;
      cursor: pointer;
      &:hover {
        svg {
          stroke: $pink;
        }
      }
      svg {
        stroke: #333;
        transition: stroke .3s ease;
      }
    }
  }
  .form-item {
    width: 80%;
    padding-top: 0;
    border: none;
    margin-bottom: 0;
    input {
      border: none;
    }
  }
  &_map {
    position: static;
    opacity: 1;
    pointer-events: auto;
    border: 1px solid #CCCCCC;
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-height: 38px;
    span {
      display: inline-block;
      padding: 8px;
      svg {
        stroke: #9C9C9C;
      }
    }
    input {
      padding: 9px 16px 9px 9px;
    }
    button {
      min-width: 50px;
    }
    .form-item, .form-action {
      width: auto;
      padding-bottom: 0;
      margin-top: 0;
    }
    .fieldset {
      width: 80%;
      display: flex;
      justify-content: flex-start;
      align-items: center;
    }
  }
}


.subscribe {

  .parsley-errors-list {
    display: none;
  }
  @media (max-width: 767px) {
    margin-top: 15px;
    display: flex;
    justify-content: space-between;
    .form-item {
      width: 70%;
    }
    .form-action {
      width: 30%;
      margin-top: 0;
      .btn {
        width: 100%;
      }
    }
  }
  @media (max-width: 500px) {
    display: block;
    .form-item {
      width: 60%;
    }
    .form-action {
      width: 40%;
    }
  }
  &_card-number {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    .form-item {
      margin-bottom: 0;
      padding-bottom: 0;
      min-width: 500px;
      @media (max-width: 767px) {
        min-width: auto;
      }
    }
  }
}


.filters-form {
  &__item {
    margin-bottom: 20px;
    text-align: left;
    position: relative;
    label {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      span {
        display: inline-block;
      }
    }
    &:last-child {
      margin-bottom: 0;
    }
    input{
      position: absolute;
      z-index: 1;
      height: 100%;
      opacity: 0;
      width: 100%;
      left: 0;
      top: 0;
      cursor: pointer;
    }

    label{
      span{
        padding-left: 20px;
        position: relative;
        display: inline-block;
        &:before,
        &:after {
          content: "";
          display: block;
          position: absolute;
        }
        &:before {
          width: 14px;
          height: 14px;
          border: 1px solid #969696;
          -webkit-border-radius: 1px;
          border-radius: 1px;
          left: 0;
          top: 3px;
        }
        &:after {
          width: 5px;
          height: 8px;
          border-right: 2px solid #fff;
          border-bottom: 2px solid #fff;
          transform: rotate(40deg);
          opacity: 0;
          left: 4px;
          top: 5px;
        }
      }
    }
    input + label {
      position: relative;
      cursor: pointer;
      width: 100%;
      height: 100%;
    }

    input:checked + label span:before {
      background-color: #474A53;
      border-color: #474A53;
    }
    input:checked + label span:after {
      opacity: 1;
    }
  }
  .form-action {
    text-align: center;
    @media (max-width: 1080px) {
      .btn {
        height: 30px;
        padding: 5px 24px;
      }
    }
  }

}


.send-form {
  &_delivery-type {
    .form-action {
      margin-top: 40px;
    }
    .shops__result-nav {
      padding: 0;
    }
    .send-form__inner {
      padding: 0 20px;
      @media (max-width: 767px) {
        padding: 0 10px;
      }
    }
    .shops__result-items {
      height: 300px;
    }
    .shops__result-item {
      padding: 20px;
      p span{
        margin: 0 3px;
      }
      p:last-child {
        margin-top: 15px;
      }
    }
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    @media (max-width: 600px) {
      display: block;
    }
  }
  &__col {
    width: calc(50% - 10px);
    text-align: left;
    @media (max-width: 768px) {
      width: calc(50% - 5px);
    }
    @media (max-width: 600px) {
      width: 100%;
      margin-bottom: 20px;
    }
  }
  &__bottom {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    .form-action {
      margin-top: 0;
    }
  }
  &__link {
    text-decoration: none;
    color: #7D7D7D;
    border-bottom: 1px solid #7D7D7D;
    font-size: 12px;
    line-height: 18px;
    transition: all .3s ease;
    display: inline-block;
    &:hover {
      opacity: .6;
    }
    &_mt {
      margin-top: 20px;
    }
  }
  &_sertificate {
    .send-form__inner {
      @media (max-width: 990px) {
        flex-wrap: wrap;
      }
    }
    .send-form__col{
       width: calc(100% / 3 - 10px);
      @media (max-width: 990px) {
        width: 100%;
        margin-bottom: 30px;
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
    h6 {
      font-size: 14px;
      line-height: 20px;
    }
    .form-action {
      text-align: center;
    }
  }
  .info {
    font-size: 14px;
    line-height: 20px;
    color: #7D7D7D;
    margin-bottom: 20px;
    display: inline-block;
  }
  .desc {
    font-size: 14px;
    line-height: 20px;
    text-align: left;
  }
  &_adress {
    .send-form__inner {
      @media (max-width: 990px) {
        flex-wrap: wrap;
      }
    }
    .send-form__col {
      width: calc(100% / 3 - 10px);
      @media (max-width: 990px) {
        width: 100%;
      }
    }
  }
}


