.page-form {
  border: 1px solid #ccc;
  &_tabs {
    .send-form__inner {
      margin-top: 20px;
    }
    .page-form__head {
      justify-content: center;
      border-bottom: none;
    }
    .page-form__tab-head {
      position: relative;
      padding-left: 20px;
      &:first-child {
        margin-right: 20px;
      }
      &:after, &:before {
        content: "";
        position: absolute;
        display: block;
        top: 50%;
        transform: translateY(-50%);
        border-radius: 50%;
      }
      &:after {
        width: 12px;
        height: 12px;
        border: 1px solid #7D7D7D;
        left: 0;
      }
      &:before {
        opacity: 0;
        transition: .3s;
        width: 8px;
        height: 8px;
        background-color: #333333;
        left: 2px;
      }
      &.active {
        &:before {
          opacity: 1;
        }
        &:after {
          border-color: #333;
        }
      }
    }
  }
  &_delivery-type {
    border: none;
    .page-form__head {
      padding: 0;
      justify-content: flex-start;
      padding: 0 20px;
      @media (max-width: 767px) {
        justify-content: space-between;
        padding: 0 10px;
      }
      @media (max-width: 600px) {
        display: block;
      }
    }
    .page-form__tab-head {
      margin-right: 22px;
      @media (max-width: 600px) {
        margin-bottom: 20px;
      }
    }
    .send-form__link {
      color: #333;
      margin-bottom: 8px;
    }
    .send-form__link-outer {
      margin-bottom: 12px;
    }
    .send-form__inner {
      padding: 0;
    }
    .search .form-action {
      margin-top: 0;
    }
    .search  {
      margin-bottom: 20px;
      padding: 0;
    }
    .shops__search-outer {
      padding-top: 30px;
      padding-left: 20px;
      @media (max-width: 767px) {
        padding-left: 0;
      }
    }
    .send-form__col {
      @media (max-width: 767px) {
        padding: 10px;
      }
      &:last-child {
        padding-right: 20px;
        @media (max-width: 767px) {
          padding: 0 10px;
        }
      }
      @media (max-width: 767px) {
        width: 100%;
        padding: 10px;
      }
    }
    .send-form__inner {
      @media (max-width: 767px) {
        flex-wrap: wrap;
      }
    }
  }
  &_registration {
    padding: 20px;
    border: 6px solid #EBEBEB;
    @media (max-width: 1080px) {
      padding: 0;
    }
    .page-form__inner {
      @media (max-width: 1080px) {
        padding: 20px;
      }
    }
    .send-form__inner {
      @media (max-width: 1080px) {
        display: block;
      }
    }
    .send-form__col {
      width: calc(100% / 3 - 10px);
      @media (max-width: 1080px) {
        width: 100%;
        margin-top: 20px;
      }
      &:nth-child(2n) {
        margin-top: 57px;
        @media (max-width: 1080px) {
          margin-top: 20px;
        }
      }
    }
    .form-action {
      text-align: center;
      @media (max-width: 1080px) {
        margin-top: 20px;
      }
    }
    .fieldset_adress {
      @media (max-width: 1080px) {
        display: block;
        .form-item {
          width: 100%;
          margin-bottom: 20px;
        }
      }
    }
  }
  .adress-block {
    margin-bottom: 20px;
    h6 {
      margin-bottom: 15px;
    }
    a {
      margin-top: 8px;
    }
    p {
      font-size: 14px;
      line-height: 20px;
      max-width: 280px;
    }
  }
  &__wrap {
    max-width: 482px;
    margin: 0 auto;
    text-align: center;
    &_code {
      margin-top: 5px;
      display: none;
      .desc {
        margin-bottom: 8px;
        text-align: left;
      }
      .order-form__bottom {
        text-align: right;
        font-size: 12px;
        color: #7D7D7D;
        margin-bottom: 25px;
        @media (max-width: 480px) {
          display: block;
        }
      }
      span {
        margin: 0 3px;
        @media (max-width: 480px) {
          display: block;
          margin: 15px 0;
        }
      }
    }
  }
  &__tab-client {
    padding: 0 20px;
    @media (max-width: 600px) {
      padding: 0 10px;
    }
  }
  &__head {
    padding: 20px;
    border-bottom: 1px solid #ccc;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__desc {
    margin-bottom: 40px;
  }
  &__tab-head {
    cursor: pointer;
    transition: opacity .3s ease;
    &:hover {
      opacity: 0.6;
    }
    &_pink {
      color: $pink;
      border-bottom: 1px solid $pink;
      padding-bottom: 3px;
    }
  }
  &__inner {
    padding: 20px 0;
  }
  &_account {
    padding: 20px 40px 40px;
    @media (max-width: 767px) {
      padding: 20px;
    }
    .page-form__head {
      padding: 0;
      .link {
        margin-left: 250px;
        @media (max-width: 1080px) {
          margin-left: 50px;
        }
        @media (max-width: 767px) {
          margin-left: 10px;
        }
      }
      @media (max-width: 800px) {
        white-space: nowrap;
        overflow-x: scroll;

      }
    }
    .page-form__tab-head {
      font-size: 14px;
      color: #CCCCCC;
      padding: 18px;
      border-bottom: 2px solid transparent;
      @media (max-width: 1200px) {
        text-align: center;
      }
      @media (max-width: 767px) {
        padding: 10px;
      }
      &.active {
        border-color: #333;
        color: #333;
      }
    }
    .page-form__inner {
      padding: 40px 0 0;
    }
  }
  &__enter-inner {
    max-width: 640px;
    margin: auto;
  }
  &__loyalty {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    @media (max-width: 1080px) {
      display: block;
    }

  }
  &__enter {
    margin-bottom: 100px;
  }
  &__loyalty-inner {
    text-align: center;
  }
  &__loyalty-col {
    h6 {
      margin-bottom: 8px;
    }
    &:first-child {
      width: 80%;
      @media (max-width: 1200px) {
        width: 70%;
      }
      @media (max-width: 1080px) {
        width: 100%;
      }
    }
    &:last-child {
      width: 18%;
      @media (max-width: 1200px) {
        width: 30%;
        }
      @media (max-width: 1080px) {
        margin-top: 20px;
        width: 100%;
      }
      .btn {
        width: 100%;
        margin-bottom: 20px;
      }
    }
  }
  &__loyalty-info {
    margin-bottom: 20px;
  }
}

