.about-page {
	.about {
		margin-bottom: 0;
	}
}

.about {
	.container {
    	@media (max-width: 767px) {
    		padding: 0 15px;
		}
	}
	&__hero {
		min-height: 770px;
		background-position: center;
		background-repeat: no-repeat;
		background-size: cover;
		padding-top: 40px;
		margin-bottom: 80px;
    	@media (max-width: 1080px) {
    		background-position: center right;
    		min-height: 600px;
		}
    	@media (max-width: 767px) {
    		position: relative;
    		&:after {
    			content: "";
    			display: block;
    			position: absolute;
    			left: 0;
    			top: 0;
    			background-color: rgba(255,255,255,.3);
    			width: 100%;
    			height: 100%;
    		}
		}
		h3 {
			max-width: 445px;
			margin-left: 100px;
    		@media (max-width: 1080px) {
    			margin-left: 0;
			}
	    	@media (max-width: 767px) {
	    		position: relative;
	    		z-index: 1;
	    	}
		}
	}
	&__hero-text {
		max-width: 630px;
		margin-left: 160px;
	    @media (max-width: 1080px) {
			margin-left: 40px;
		}
		@media (max-width: 767px) {
    		position: relative;
    		z-index: 1;
    	}
	}
	p {
		margin-bottom: 20px;
		font-size: 20px;
		line-height: 24px;
	}
	&__inner {
		display: flex;
		justify-content: space-between;
		align-items: center;
		@media (max-width: 767px) {
			flex-wrap: wrap;
			padding-bottom: 40px;
		}
		&_top {
			.about__col_l {
				.about__img {
					min-height: 465px;
					position: relative;
					z-index: 1;
					@media (max-width: 1080px) {
						min-height: 260px;
					}
					@media (max-width: 767px) {
						min-height: 300px;
					}
				}
				.about__img-wrap {
					position: relative;
					&:before {
						content: "";
						display: block;
						position: absolute;
						top: -40px;
						right: -40px;
						width: 355px;
						height: 220px;
						background-color: #F9EDF0;
					}
				}
				.about__info {
					padding-top: 70px;
					@media (max-width: 1080px) {
						padding-top: 40px;
					}
					h3 {
						margin-bottom: 8px;
					}
					&:last-child {
						padding-bottom: 60px;
						@media (max-width: 767px) {
							padding-bottom: 30px;
						}
					}
				}
			}
			.about__col_r {
				p {
					font-size: 16px;
					line-height: 20px;
					margin-bottom: 80px;
					max-width: 310px;
					@media (max-width: 767px) {
						margin-bottom: 60px;
					}
				}
				.about__img {
					min-height: 626px;
					@media (max-width: 1080px) {
						min-height: 300px;
					}
				}
				.about__img-wrap {
					padding-left: 40px;
					@media (max-width: 1080px) {
						padding-left: 0;
					}

				}
			}
		}
		&_bot {
			margin-top: 70px;
			@media (max-width: 1080px) {
				flex-wrap: wrap;
				margin-bottom: 40px;
			}
			.about__img-wrap {
				margin-bottom: 80px;
				position: relative;
				padding-bottom: 248px;
				@media (max-width: 767px) {
					padding-bottom: 349px;
					margin-bottom: 40px;
				}
				&:before {
					content: "";
					display: block;
					position: absolute;
					top: 270px;
					left: 230px;
					width: 355px;
					height: 364px;
					background-color: #F9EDF0;
					@media (max-width: 767px) {
						width: 250px;
						height: 200px;
					}
					@media (max-width: 500px) {
						width: 100%;
						left: 0;
					}
				}
			}
			.about__img {
				&:first-child {
					width: 450px;
					height: 600px;
					position: relative;
					z-index: 2;
					margin-left: 100px;
					@media (max-width: 1080px) {
						margin-left: 0;
					}
					@media (max-width: 767px) {
						width: 250px;
						height: 400px;
					}

				}
				&:last-child {
					width: 360px;
					height: 500px;
					position: absolute;
					z-index: 3;
					left: 0;
					top: 348px;
					@media (max-width: 1080px) {
						left: auto;
						right: 0;
					}
					@media (max-width: 767px) {
						width: 250px;
						height: 400px;
					}
				}
			}
			.about__col_r {
				display: flex;
				flex-direction: column;
				align-items: center;
				justify-content: center;
				@media (max-width: 1080px) {
					width: 100%;
				}
				p {
					padding-left: 40px;
				}

			}
			.about__col_l {
				@media (max-width: 1080px) {
					width: 100%;
				}
			}
		}
	}
	&__col {
		&:first-child {
			padding: 0;
		}
		&_l {
			width: 52%;
			@media (max-width: 767px) {
				width: 100%;
			}
		}
		&_r {
			width: 42%;
			@media (max-width: 767px) {
				width: 100%;
			}
		}
	}
	&__map {
		margin-top: 120px;
		min-height: 620px;
		background-position: center;
		background-repeat: no-repeat;
		background-size: cover;
		@media (max-width: 1080px) {
			min-height: 400px;
		}
	}
	&__map-info {
		background-color: #333333;
		color: #FFF;
		padding: 48px 97px;
		max-width: 615px;
		@media (max-width: 1080px) {
			padding: 30px;
			max-width: 400px;
		}

		@media (max-width: 767px) {
			max-width: 100%;
		}
		@media (max-width: 480px) {
			padding: 20px 15px;
		}
		&:first-child {
			transform: translateY(-120px);
			margin-bottom: 160px;
			@media (max-width: 1080px) {
				margin-bottom: 15px;
			}
			h3 {
				margin-bottom: 15px;
			}
			p {
				max-width: 200px;
			}
		}
		&_bot {
			background-color: $pink;
			padding: 60px 40px;
			margin-left: 100px;
			@media (max-width: 1080px) {
				padding: 30px;
			}
			@media (max-width: 767px) {
				margin-left: 0;
				padding: 30px;
			}
			@media (max-width: 480px) {
				padding: 20px 15px;
			}
		}
		p {
			margin-bottom: 0;
		}
	}
}

.benefits {
	margin-bottom: 80px;
	h3 {
		text-align: center;
	}
	&__list {
		display: flex;
		flex-wrap: wrap;
		margin: 0 -10px;
		@media (max-width: 767px) {
			margin: 0 -5px;
		}
	}
	&__item-outer {
		flex: 0 0 33.33%;
		padding: 0 10px;
		margin-bottom: 20px;
		align-self: stretch;
		@media (max-width: 767px) {
			flex: 0 0 50%;
			padding: 0 5px;
			margin-bottom: 10px;
		}
	}
	&__item {
		background-color: #F8F8F8;
		padding: 32px;
		min-height: 210px;
		text-align: center;
		height: 100%;
		@media (max-width: 767px) {
			padding: 20px 10px;
		}
		svg {
			margin-bottom: 15px;
		}
	}
}
