.shops {
	margin-bottom: 40px;
	.page-title {
		text-align: center;
	}
	&__inner {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		border: 1px solid #CCCCCC;
    	@media (max-width: 768px) {
    		flex-wrap: wrap;
		}
	}
	&__col {
		&:first-child {
			width: 30%;
			height: 520px;
			overflow: hidden;
    		@media (max-width: 1180px) {
    			width: 40%;
			}
    		@media (max-width: 768px) {
    			width: 100%;
    			height: 300px;
				border-bottom: 1px solid #CCCCCC;

			}
		}
		&:last-child {
			width: 70%;
    		@media (max-width: 1180px) {
    			width: 60%;
			}
    		@media (max-width: 768px) {
    			width: 100%;
			}
		}
	}
	.ov-a::-webkit-scrollbar-track {
	  background-color: #EBEBEB;
	}
	.ov-a::-webkit-scrollbar {
	  width: 4px;
	  height: 4px;
	  background-color: #EBEBEB;
	}
	.ov-a::-webkit-scrollbar-thumb {
	  background-color: #333333;
	  width: 4px;
	  height: 4px;
	}
	&__result-item {
		cursor: pointer;
		transition: background .3s ease;
		padding: 20px;
		h6 {
			font-size: 14px;
			line-height: 20px;
			text-transform: uppercase;
			font-weight: 700;
			margin-bottom: 8px;
		}
		p {
			font-size: 12px;
			line-height: 18px;
		}
		&:hover {
			background-color: #EBEBEB;
		}
		&.active {
			background-color: #EBEBEB;
		}
	}
	&__map-outer {
		padding: 20px;
	}
	&__result-nav {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 0 20px;
		font-size: 14px;
		line-height: 20px;
		margin-bottom: 8px;
	}
	&__result-count {
		span {
			display: inline-block;
			margin-right: 3px;
		}
	}
	&__clear {
		text-decoration: none;
		border-bottom: 1px solid #333333;
		transition: border-color .3s ease;
		&:hover {
			border-color: transparent;
		}
	}
}

.map {
	height: 520px;

}


.map-popup {
	max-width: 220px;
	&__title {
		font-size: 12px;
		line-height: 18px;
		text-transform: uppercase;
		margin-bottom: 8px;
	}
	a, p{
		font-weight: 400;
		font-size: 10px;
		line-height: 16px;
		text-decoration: none;
	}
	&__link {
		display: inline-block;
		margin-top: 16px;
		border-bottom: 1px solid #333333;
		transition: border-color .3s ease;
		&:hover {
			border-color: #FFF;
		}
	}
	.gm-ui-hover-effect {
		top: -2px;
		right: -2px;
	}
}
.gm-style .gm-style-iw-c {
	left: 70px;
}
