.sertificate {
  &__main {
    min-height: 440px;
    padding: 0 60px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    @media (max-width: 767px) {
      padding: 0 30px;
    }
  }
  &__info {
    max-width: 400px;
    text-align: center;
    p {
      strong {
        margin: 0;
      }
      &.desc {
        text-transform: uppercase;
      }
    }
    h2 {
      color: $pink;
      text-transform: uppercase;
      font-size: 60px;
      line-height: 70px;
      margin-bottom: 20px;
      @media (max-width: 767px) {
        font-size: 30px;
      }
    }
    .btn {
      margin-top: 50px;
    }
  }
  &__inner {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -10px;
    margin-top: 40px;
    margin-bottom: 80px;
  }
  &__item-outer {
    flex: 0 0 33.33%;
    max-width: 33.33%;
    padding: 0 10px;
    margin-bottom: 20px;
    @media (max-width: 767px) {
      flex: 0 0 50%;
      max-width: 50%;
    }
    @media (max-width: 500px) {
      flex: 0 0 100%;
      max-width: 100%;
    }
  }
  &__item {
    position: relative;
    text-align: center;
    margin-bottom: 40px;
    h4 {
      margin-bottom: 8px;
    }
    p {
      margin-bottom: 20px;
    }
  }
  &__img {
    padding-bottom: 65%;
    display: block;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    transition: opacity .3s ease;
    @media (max-width: 500px) {
      background-size: contain;
    }
  }
}

.sertificate-form {
  margin-bottom: 40px;
  .page-title {
    text-align: center;
  }
  &__inner {
    border: 4px solid #EBEBEB;
    padding: 40px;
    @media (max-width: 990px) {
      padding: 20px;
    }
    @media (max-width: 480px) {
      padding: 30px 10px;
    }
  }
}
