.pagination-nav {
  margin: 30px 0;
  text-align: center;
  ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  a, span {
    display: block;
    color: #7D7D7D;
    text-decoration: none;
    border-bottom: 1px solid transparent;
    padding: 0 4px;
    transition: .3s ease;
    &:hover  {
      color: $pink;
    }
    // @media (max-width: 1440px) {
    //   font-size: 12px;
    // }
  }
  .active {
    color: $pink;
    border-bottom: 1px solid $pink;
    padding: 0 4px;
  }

  .prev,
  .next{
    a {
      padding: 0;
      width: 6px;
      height: 6px;
      border-top: 2px solid #7D7D7D;
      border-right: 2px solid #7D7D7D;
      transition: border .3s ease;
      &:hover {
        border-top: 2px solid $pink;
        border-right: 2px solid $pink;
      }
    }
  }
  .prev {
    transform: rotate(-135deg);
    margin-right: 28px;
    @media (max-width: 600px) {
      margin-right: 5px;
    }
  }
  .next{
    transform: rotate(45deg);
    margin-left: 28px;
    @media (max-width: 600px) {
      margin-left: 5px;
    }
  }
}


