.category {
  h2 {
    font-size: 20px;
    line-height: 32px;
    text-align: center;
  }
  &__desc {
    font-size: 14px;
    line-height: 20px;
    text-align: center;
  }
  &__top {
    margin-bottom: 40px;
  }
  &__bottom {
    padding-top: 40px;
    margin-bottom: 40px;
    border-top: 1px solid #ccc;
    h5 {
      text-align: center;
      margin-bottom: 20px;
    }
  }
  &__nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 14px;
    line-height: 20px;
    color: #7D7D7D;
    margin-bottom: 20px;
  }
  &__count {
    margin-right: 40px;
    display: inline-block;
    span {
      margin-right: 5px;
    }
    @media (max-width: 1200px) {
      margin-right: 20px;
    }
  }
  &__viev {
    display: inline-block;
    span {
      margin-right: 20px;
      display: inline-block;
      @media (max-width: 1200px) {
        margin-right: 10px;
      }
    }
    @media (max-width: 1080px) {
      display: none;
    }
  }
  &__viev-item{
    display: inline-block;
    cursor: pointer;
    margin: 0 4px;
    &.active {
      color: #333333;
      border-bottom: 2px solid #333333;
      font-weight: 700;
    }
  }
}

.filters {
  position: relative;
  @media (max-width: 1080px) {
    display: block;
    &.active {
      .filters__inner {
        transform: translateX(0);
        pointer-events: auto;
        opacity: 1;
      }
      span {
        display: none;
        &:first-child {
          display: none;
        }
        &:last-child {
          display: block
        }
      }
    }
  }
  &__btn {
    display: none;
    span {
      display: none;
      &:first-child {
        display: block;
      }
    }
    @media (max-width: 1080px) {
      display: block;
      padding: 7px 20px;
    }
  }
  &__inner {
    @media (max-width: 1080px) {
      background-color: #FFF;
      position: absolute;
      left: 0;
      top: 55px;
      z-index: 99;
      min-width: 300px;
      transform: translateX(-100%);
      opacity: 0;
      pointer-events: none;
      padding-bottom: 20px;
    }
  }
  &__item {
    display: inline-block;
    color: #333;
    cursor: pointer;
    position: relative;
    @media (max-width: 1080px) {
      display: block;
      background-color: #FFF;
      border-bottom: 1px solid #CCCCCC;
      &:last-child {
        border-bottom: none;
      }
    }
    &:last-child {
      margin-bottom: 0;
    }
    &.active {
      .filters__head {
        &:after {
          top: 8px;
          transform: rotate(-135deg);
          @media (max-width: 1080px) {
            top: 18px;
          }
        }
      }
      .filters__body {
        pointer-events: auto;
        transform: translateY(0);
        opacity: 1;
      }
    }
  }
  &__head {
    font-size: 14px;
    line-height: 20px;
    padding-right: 30px;
    margin-right: 40px;
    position: relative;
    transition: color .3s ease;
    padding-bottom: 10px;
    @media (max-width: 1200px) {
      margin-right: 20px;
    }
    @media (max-width: 1080px) {
      padding: 15px;
    }
    &:after {
      content: "";
      display: block;
      position: absolute;
      right: 10px;
      top: 5px;
      width: 6px;
      height: 6px;
      border-bottom: 1px solid #333;
      border-right: 1px solid #333;
      transform: rotate(45deg);
      transition: all .3s ease;
      @media (max-width: 1080px) {
        top: 20px;
        content: none;
      }
    }
    &:hover{
      color: $pink;
      &:after {
        border-color: $pink;
      }
    }
  }
  &__body {
    position: absolute;
    opacity: 0;
    pointer-events: none;
    z-index: 99;
    top: 100%;
    left: 0;
    transform: translateY(-20%);
    background-color: #FFF;
    border: 1px solid #CCCCCC;
    padding: 24px 16px 16px;
    min-width: 260px;
    @media (max-width: 1200px) {
      min-width: 220px;
    }
    @media (max-width: 1080px) {
      display: block;
      position: static;
      opacity: 1;
      pointer-events: auto;
      transform: none;
      min-width: auto;
      border: none;
      padding: 10px 15px;
    }
  }
}
