.header {
  width: 100%;
  &__static {
    position: relative;
    z-index: 10;
  }
  &__container {
    max-width: 1400px;
    margin: 0 auto;
    padding: 0 23px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media (max-width: 1080px) {
      padding: 0 10px;
    }
    .logo {
      width: 190px;
      @media (max-width: 1080px) {
        width: 150px;
      }
    }.perfect {
      width: 140px;
      margin-left: 25px;
      padding-left: 20px;
      border-left: 1px solid #cccccc;
    }
  }
  &__middle {
    position: relative;
    padding: 25px 0;
    background-color: #FFF;
    @media (max-width: 1080px) {
      position: fixed;
      z-index: 9999;
      padding: 22px 0;
      width: 100%;
    }
    a {
      font-size: 16px;
      line-height: 20px;
      transition: color .3s ease;
      &:hover {
        color: $pink;
      }
    }
    &.search-active {
      @media (min-width: 1080px) {
        .search_desk {
          opacity: 1;
          pointer-events: auto;
        }
        .on-search {
          opacity: 0;
          pointer-events: none;
        }
        .cansel-search {
          opacity: 1;
          pointer-events: auto;
        }
      }
      @media (max-width: 1080px) {
        .search_mob {
          transform: translateX(0);
          opacity: 1;
          pointer-events: auto;
        }
      }
    }
  }
  &__middle-group {
    position: relative;
    width: 45%;
    text-align: center;
    @media (max-width: 1200px) {
      width: 35%;
    }
    @media (max-width: 1080px) {
      display: none;
    }
  }
  &__top {
    background: $color;
    padding: 4px 0 5px;
    @media (max-width: 1080px) {
      display: none;
    }
    .link, .item-language-main {
      color: #FFF;
      font-size: 12px;
      line-height: 18px;
      margin: 0 20px;
      text-decoration: none;
      border-bottom: 1px solid #fff;
      transition: all .3s ease;
      &:hover {
        border-color: transparent;
      }
    }
    .header__container {
      justify-content: center;
    }
  }
  &__bottom {
    background-color: $pink;
    transition: background .3s ease;
    position: relative;
    z-index: 99;
    @media (max-width: 1080px) {
      display: none;
    }
    .logo {
      display: none;
    }
  }

  &__foot-mobile {
    display: none;
  }

  &__btns {
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  &__catalog {
    width: 100%;
    display: flex;
    justify-content: space-between;
    a {
      color: #FFF;
      font-size: 12px;
      line-height: 18px;
      text-decoration: none;
      border-bottom: 1px solid transparent;
      transition: border .3s ease;
      &:hover {
        border-color: #FFF;
      }
      @media (max-width: 1200px) {
        font-size: 11px;
      }
      @media (max-width: 1080px) {
        font-size: 14px;
      }
    }
  }
  &__catalog-item {
    padding: 16px 0;
    @media (min-width: 1080px) {
      &:hover {
        &>.main-menu {
          transition: max-height .5s ease, opacity .5s ease;
          opacity: 1;
          pointer-events: auto;
          max-height: 300px;
        }
      }
    }
    @media (max-width: 1080px) {
      padding: 10px 0;
      &.active {
        &>.main-menu {
          transition: max-height .5s ease, opacity .5s ease;
          opacity: 1;
          pointer-events: auto;
          height: 100%;
          top: 0;
        }
      }
    }

  }
  &__wish-list {
    margin-right: 27px;
    display: inline-block;
    svg {
      path {
        stroke: #333;
        transition: all .3s ease;
      }
    }
    &:hover {
      svg {
        path {
          stroke: $pink;
        }
      }
    }
    @media (max-width: 1080px) {
      display: none;
      margin-bottom: 15px;
    }
  }
  &__account {
    margin: 0 63px;
    a{
      &:first-child {
        svg {
          margin-right: 10px;
        }
      }
    }
    .divide {
      margin: 0 5px;
      font-size: 12px;
    }
    @media (max-width: 1080px) {
      display: none;
    }
  }
  .cansel-search {
    display: inline-block;
    padding-left: 28px;
    position: absolute;
    left: -2px;
    top: 2px;
    opacity: 0;
    pointer-events: none;
    &:before, &:after {
      content: "";
      display: block;
      position: absolute;
      width: 1px;
      height: 10px;
      background-color: #333;
      top: 50%;
      left: 12px;
      transform: translate(-50%, -50%);
    }
    &:before {
      transform: translate(0, -50%) rotate(45deg);
    }
    &:after {
      transform: translate(0, -50%) rotate(-45deg);
    }
    &:hover {
      &:before, &:after {
        background-color: $pink;
      }
    }
  }
  &__search  {
    position: relative;
    .on-search {
      @media (max-width: 1080px) {
        margin-right: 10px;
        span {
          display: none;
        }
      }
    }
  }
  &__search , &__account {
    a {
      display: inline-block;
      text-decoration: none;
      font-size: 14px;
      line-height: 20px;
      @media (max-width: 1200px) {
        font-size: 12px;
      }
      @media (max-width: 1080px) {
        font-size: 14px;
      }
    }
  }
  &__basket, &__wish-list,  &__account a:first-child, &__search {
    svg {
      path {
        stroke: #333;
        transition: all .3s ease;
      }
    }
    &:hover {
      svg {
        path {
          stroke: $pink;
        }
      }
    }
  }
  &__basket {
    position: relative;
    .count {
      position: absolute;
      background-color: $pink;
      color: #FFF;
      font-size: 10px;
      line-height: 1;
      border-radius: 50%;
      display: inline-block;
      top: -3px;
      right: -3px;
      min-width: 14px;
      min-height: 14px;
      padding: 2px 4px 1px;
    }
  }
  &__nav {
    margin: 0 10px;
    display: flex;
    ul {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    li {
      display: inline-block;
      margin: 0 15px;
    }
    a {
      font-size: 15px;
      text-decoration: none;
      display: inline-block;
      &.active {
        text-decoration: none;
        color: #f57b0a;
      }
      @media (max-width: 1200px) {
        font-size: 12px;
      }
    }
  }
  // &__perfect {
  //   color: #afce4d;
  //   font-size: 15px;
  //   font-weight: bold;
  //   text-decoration: none;
  //   flex-shrink: 0;.icon  {
  //   width: 18px;
  //   height: 35px;
  //   fill: #afce4d;
  //   margin-right: 5px;
  //   }
  // }
  &__banner {
    background-color: #F8F8F8;
    font-size: 14px;
    line-height: 20px;
    padding: 15px;
    color: #BD3D5C;
    @media (max-width: 1080px) {
      position: relative;
      top: 70px;
      z-index: 99;
    }
    @media (min-width: 767px) {
      font-size: 12px;
    }
    .header__container {
       justify-content: center;
       align-items: center;
      // @media (min-width: 767px) {
        flex-wrap: wrap;
      // }
    }
  }
  &__banner-info {
    text-align: center;
    span {
      text-transform: uppercase;
      font-weight: bold;
      margin: 0 3px;
    }
    @media (max-width: 767px) {
      width: 100%;
    }
  }
  &__banner-link {
    font-size: 14px;
    line-height: 20px;
    display: inline-block;
    color: #BD3D5C;
    border-bottom: 1px solid #BD3D5C;
    transition: .3s ease;
    text-decoration: none;
    cursor: pointer;
    margin-left: 40px;
    &:hover {
      opacity: 0.6;
    }
    @media (max-width: 767px) {
      font-size: 12px;
      margin-left: 0;
    }
  }
}

.search-suggest {
  display: none;
  position: absolute;
  top: 99%;
  width: 100%;
  height: auto;
  background-color: #FFF;
  z-index: 999;
  padding: 8px 0 19px;
  @media (max-width: 1080px) {
    top: 195%;
  }
  &__container {
    max-width: 770px;
    margin: auto;
    padding: 0 15px;
  }
  &__title {
    text-transform: uppercase;
    color: #7D7D7D;
  }
  &__inner {
    padding: 20px 0 10px;
    max-height: 110px;
    overflow: hidden;

  }
  &__item {
    background-color: #F8F8F8;
    border-radius: 20px;
    display: inline-block;
    text-decoration: none;
    margin-right: 20px;
    margin-bottom: 10px;
    padding: 8px 25px 8px 15px;
    &:last-child {
      margin-right: 0;
    }
    span {
      display: inline-block;
      position: relative;
      width: 10px;
      height:10px;
      margin-left: 5px;
      cursor: pointer;
      transition: opacity .3s ease;
      &:hover {
        opacity: 0.7;
      }
      &:before, &:after {
        content: "";
        display: block;
        position: absolute;
        width: 1px;
        height: 100%;
        background-color: #333;
        top: 50%;
        left: 12px;
        transform: translate(-50%, -50%);
      }
      &:before {
        transform: translate(0, -50%) rotate(45deg);
      }
      &:after {
        transform: translate(0, -50%) rotate(-45deg);
      }
    }
  }
}
.search-help {
  display: none;
  position: absolute;
  top: 99%;
  width: 100%;
  height: auto;
  background-color: #FFF;
  z-index: 999;
  padding: 8px 0 19px;
  @media (max-width: 1080px) {
    top: 194%;
  }
  &__container {
    max-width: 820px;
    margin: auto;
    padding: 0 15px;
    @media (max-width: 1400px) {
      max-width: 760px;
    }
  }
  &__inner {
    max-width: 440px;
  }
  &__item {
    display: block;
    text-decoration: none;
    border-bottom: 1px solid #CCCCCC;
    font-size: 14px;
    line-height: 20px;
    padding: 8px 0;
    transition: .3s ease;
    &:hover {
      background-color: #F8F8F8;
      color: #333;
    }
  }
}
.main-menu {
  position: absolute;
  background-color: #FFF;
  width: 100%;
  left: 0;
  top: 100%;
  max-height: 0;
  border-top: 1px solid #ccc;
  padding: 20px 0;
  // transform: translateY(-100%);
  transition: max-height .3s ease, opacity .3s ease;
  overflow: hidden;
  pointer-events: none;
  opacity: 0;
  z-index: 99;
  @media (max-width: 1080px) {
    max-height: 100%;
    height: 100%;
    padding: 20px 10px;
  }
  a {
    display: inline-block;
    font-size: 14px;
    text-decoration: none;
    margin-bottom: 8px;
    transition: color .3s ease;
    color: #333;
    &:hover {
      color: $pink;
    }
  }
  sup {
    color: #7D7D7D;
    font-size: 10px;
    margin-left: 3px;
  }
  &__close {
    position: absolute;
    width: 25px;
    height: 25px;
    right: 15px;
    top: 15px;
    &:before, &:after {
      content: "";
      display: block;
      position: absolute;
      width: 1px;
      height: 20px;
      background-color: #333;
      top: 50%;
      left: 12px;
      transform: translate(-50%, -50%);
    }
    &:before {
      transform: translate(0, -50%) rotate(45deg);
    }
    &:after {
      transform: translate(0, -50%) rotate(-45deg);
    }
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    max-width: 1400px;
    margin: 0px auto;
    padding: 0px 23px;
    margin: auto;
    @media (max-width: 1080px) {
      display: block;
      padding-right: 25px;
      padding-bottom: 80px;
      overflow-y: auto;
      height: 100%;
    }
  }
  &__col {
    width: 18.4%;
    &:last-child {
      text-align: right;
    }
    @media (max-width: 1080px) {
      width: 100%;
      border-bottom: 1px solid #CCCCCC;
      margin-bottom: 10px;
      &:last-child {
        text-align: left;
        border-bottom: none;
      }
    }
    @media (min-width: 1080px) {
      &_lg {
        width: 25%;
        padding-right: 120px;
        position: relative;
        &:after {
          content: "";
          display: block;
          position: absolute;
          height: 100%;
          width: 1px;
          background-color: #CCCCCC;
          top: 0;
          right: 100px;
        }
      }
    }
  }
  h4 {
    text-transform: uppercase;
    font-size: 14px;
    margin-bottom: 20px;
    @media (max-width: 1080px) {
      margin-bottom: 10px;
    }
  }
  &__category {
    width: 100%;
    max-width: 160px;

  }
  &__category-img {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: block;
    width: 100%;
    min-height: 225px;
  }
  &__category-title {
    display: block;
    text-align: center;
    background-color: #EBEBEB;
    text-transform: uppercase;
    padding: 4px;
    margin-bottom: 0;
    color: $pink;
  }
}
@media (min-width: 1080px) {
  .scroll {
   .header__bottom {
      // padding: 16px 0;
      background-color: #FFF;
      border-bottom: 2px solid $pink;
      position: fixed;
      left: 0;
      top: 0;
      width: 100%;
      z-index: 9;
      transition: background .3s ease;
      .header__catalog a {
        color: #333;
        border-bottom: none;
        transition: color .3s ease;
        &:hover {
          color: $pink;
        }
        &.spec {
          color: $pink;
          &:hover {
            opacity: 0.6;
          }
        }
      }
      .logo {
        max-width: 145px;
        margin-right: 90px;
        display: block;
      }
    }
    .spec {
      color: $pink;
    }
  }
}


.switcher-dropdown {
  position: relative;
  &.active {
    .language-dropdown {
      opacity: 1;
      transform: none;
      pointer-events: auto;
    }
  }
  @media (min-width: 1201px) {
    &:hover {
      .language-dropdown {
        opacity: 1;
        transform: none;
        pointer-events: auto;
      }
    }
  }
  .language-dropdown {
    position: absolute;
    left: 10px;
    top: 100%;
    z-index: 102;
    transform: translateY(35px);
    opacity: 0;
    pointer-events: none;
    transition:.3s;
    cursor: pointer;
    padding: 16px;
    background-color: #FFF;
    border: 1px solid #333333;
    @media (max-width: 1200px) {
      left: -6px;
    }
  }
  .item-language-main > span {
    cursor: pointer;
  }
  li {
    margin-bottom: 16px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  a {
    display: inline-block;
    text-decoration: none;
    color: #333;
    transition: opacity .3s ease;
    &:hover {
      opacity: 0.6;
    }
  }
}

.hamburger {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  cursor: pointer;
  padding: 3px;
  position: relative;
  width: 58px;
  height: 28px;
  display: none;
  &:before, &:after {
    content: "";
    position: absolute;
    left: 0;
    width: 100%;
    height: 2px;
    background: #333;
    transition: 0.3s;
  }&:before {
    top: 12px;
    width: 22px;
  }&:after {
    width: 13px;
    bottom: 6px;
  }
  span {
    position: absolute;
    top: 14px;
    left: 0;
    width: 100%;
    height: 5px;
    background-color: #333;
    transition: background 0.3s;
    transition-delay: 0.3s;
  }
}
.openNav {
  .header__nav {
    transform: none;
  }.hamburger {
    z-index: 10;
    span {
      display: none;
    }&:before, &:after  {
      top: 46%;
      width: 22px;
    }&:before {
      transform: rotate(45deg);
    }&:after {
      transform: rotate(-45deg);
    }
  }
}
.mobile-menu {
  display: none;
}
@media (max-width: 1080px) {
  .hamburger {
    display: block;
    flex-shrink: 0;
  }
  .header__bottom {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 999;
    background-color: #FFF;
    overflow: hidden;
    transform: translateX(100%);
    transition: transform 0.3s;
    width: 100%;
    overflow-y: hidden;
    display: block;
    height: 100vh;
    padding-top: 70px;
    // height: calc(100vh - 70px);
    // margin-top: 70px;
    a {
      display: inline-block;
      text-decoration: none;
    }
    .header__container {
      position: relative;
      display: block;
      // overflow-y: auto;
      height: 100%;
    }
  }
  .header__catalog {
    display: block;
    margin-bottom: 20px;
  }
  .header__foot-mobile {
    display: block;
    .header__account {
      display: block;
      margin: 0;
      margin-bottom: 15px;
    }
    .link {
      margin-bottom: 15px;
    }
  }
  .header__catalog a {
    color: #333;
  }
  .header__catalog-item {
    .header__link_l1 {
      position: relative;
      display: block;
      width: 100%;
      &:after {
        content: "";
        display: block;
        position: absolute;
        right: 10px;
        top: 50%;
        width: 8px;
        height: 8px;
        border-top: 2px solid #61616A;
        border-right: 2px solid #61616A;
        transform: translateY(-50%) rotate(45deg);
        transition: all .3s ease;
      }
    }
  }

  .openNav {
    overflow: hidden;
    max-height: 100vh;
    .header__bottom {
      transform: translateX(0);
    }
  }
}
