.brands-page {
  margin-bottom: 40px;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    @media (max-width: 1080px) {
      flex-wrap: wrap;
    }
  }
  h2 {
    text-transform: uppercase;
  }
  &__nav{
    width: 16%;
    @media (max-width: 1080px) {
      width: 18%;
    }
    @media (max-width: 767px) {
      width: 100%;
      margin-bottom: 20px;
      li {
        display: inline-block;
      }
    }
  }

  &__main {
    width: 55%;
    border-top: 1px solid #CCC;
    &_faq {
      border-top: none;
    }
    @media (max-width: 1080px) {
      width: 75%;
    }
    @media (max-width: 767px) {
      width: 100%;
    }
  }
  &__title {
    margin-left: 19%;
    @media (max-width: 1080px) {
      margin-left: 25%;
    }
    @media (max-width: 767px) {
      display: none;
    }
    &_mob {
      display: none;
      @media (max-width: 767px) {
        margin-left: 0;
        margin-top: 40px;
        text-align: center;
        display: block;
      }
    }
  }
  &__link {
    display: inline-block;
    text-decoration: none;
    padding: 5px 14px;
    font-size: 14px;
    font-weight: 700;
    transition: opacity .3s ease;
    &:hover {
      opacity: 0.6;
    }
    &.active {
      background-color: #333;
      color: #FFF;
    }
  }
}

.contact-sm {
  width: 23%;
  padding-left: 90px;
  @media (max-width: 1440px) {
    padding-left: 30px;
  }
  @media (max-width: 1080px) {
    padding-left: 0;
    width: 100%;
  }
  a {
    display: block;
    text-decoration: none;
    text-transform: uppercase;
    margin-bottom: 20px;
    font-size: 14px;
    line-height: 20px;
    color: #7D7D7D;
    font-weight: 500;
    transition: opacity .3s ease;
    &:hover {
      opacity: 0.6;
    }
    svg {
      display: inline-block;
      margin-right: 12px;
    }
    strong {
      color: #333;
    }
  }
  &__title {
    text-transform: uppercase;
    color: #7D7D7D;
    margin-bottom: 20px;
    font-size: 14px;
    line-height: 20px;
    font-weight: 500;

  }
  &__time {
    font-size: 14px;
    line-height: 20px;
    margin-bottom: 20px;
    span {
      margin: 0 3px;
    }
  }
}


.brand-info {
  margin-top: 20px;
  font-size: 14px;
  line-height: 20px;
  &__title {
    font-weight: 700;
    margin-bottom: 8px;
  }
  &_faq {
    margin-top: 0;
  }
  .center {
    text-align: center;
    margin: 0 auto;
  }
  table {
    margin-top: 20px;
    border-collapse: collapse;
    font-size: 14px;
    line-height: 20px;
    @media (max-width: 1080px) {
      margin-bottom: 40px;
    }
    @media (max-width: 767px) {
      font-size: 12px;
    }

    th {
      text-transform: uppercase;
      font-weight: 500;
      background-color: #ccc;
      border: 1px solid #fff;
      padding: 20px;
      @media (max-width: 767px) {
        padding: 15px 10px;
      }
    }
    th, td {
      width: 33.33%;
    }
    td {
      padding: 10px 20px;
      border: 1px solid #CCCCCC;
      vertical-align: top;
      @media (max-width: 767px) {
        padding: 10px;
      }
    }
    strong {
      margin: 0;
    }
    ul {
      margin-left: 14px;
      li {
        list-style-type: disc;
        p {
          margin-bottom: 0;
        }
      }
    }
  }
  strong {
    margin: 0 3px;
  }
  ol {
    padding-left: 20px;
    li {
      list-style-type: decimal;
    }
  }
  h4 {
    text-transform: uppercase;
    font-size: 14px;
    margin: 20px 0;
  }
  p {
    font-size: 14px;
    line-height: 22px;
    margin-bottom: 20px;
    @media (max-width: 767px) {
      font-size: 12px;
    }
  }
}


.acordeon {
  margin-bottom: 40px;
  ol{
    margin: 0;
    padding: 0;
    counter-reset: section;
  }
  p {
    max-width: 100%;
    text-align: left;
  }
  .item {
    &__body{
      display: none;
      &.active{
        display: block;
      }
    }
    &__head {
      position: relative;
      background-color: #F8F8F8;
      color: #333;
      line-height: 1;
      padding: 16px 40px 16px 16px;
      cursor :pointer;
      transition: opacity .3s ease;
      margin-bottom: 20px;
      &:hover {
        opacity: 0.7;
      }
      p {
        font-weight: 700;
        margin-bottom: 0;
      }
      &:after{
        content: "";
        display: block;
        position: absolute;
        right: 23px;
        top: 48%;
        transform: translateY(-50%) rotate(135deg);
        width: 10px;
        height: 10px;
        border-top: 2px solid #333;
        border-right: 2px solid #333;
        transition: all .3s ease;
      }
      &.active {
        &:after {
          transform: translateY(-50%) rotate(-45deg);
        }
      }
    }
    &__body {
      padding: 0 20px;
    }
  }
}
