.loyalty-program {
  &__hero {
    min-height: 473px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
    @media (max-width: 767px) {
      background-position: center right;
      min-height: 350px;
    }
    h3 {
      text-align: center;
    }
  }
  &__inner {
    padding: 40px 0;
  }
  &__btn-outer{
    text-align: center;
    margin-top: 50px;
  }
}
