.footer {
  background: #F8F8F8;
  font-size: 14px;
  @media (max-width: 767px) {
    padding-top: 32px;
    padding-bottom: 24px;
  }
  &__top-mob {
    @media (max-width: 767px) {
      .footer__title {
        text-align: center;
      }
    }
  }
  &__bottom {
  	border-top: 1px solid #9C9C9C;
  	padding: 35px 0;
  	display: flex;
  	justify-content: space-between;
  	align-items: center;
    @media (max-width: 767px) {
      border-top: none;
      display: block;
      padding: 35px 0 25px;
      .logo {
        display: none;
      }
      .link {
        border-bottom: 1px solid #333;
        font-size: 12px;
      }
    }
  }
  &__row {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding: 40px 0;
    @media (max-width: 767px) {
      display: block;
      padding-bottom: 0;
    }
  }
  &__col {
    width: 25%;
    @media (max-width: 1080px) {
      padding-right: 8px;
    }
    @media (max-width: 767px) {
      width: 100%;
      padding-right: 0;
    }
  }
  li {
    margin-bottom: 5px;
  }
  ul {
    margin-bottom: 24px;
  }
  a {
    text-decoration: none;
    font-size: 14px;
    line-height: 20px;
    color: #7D7D7D;
    transition: color .3s ease;
    &:hover {
      color: $pink;
    }
    @media (max-width: 767px) {
      color: #333;
    }
  }
  h6 {
    text-transform: uppercase;
    line-height: 20px;
  	padding-right: 70px;
  	margin-bottom: 0;
    @media (max-width: 767px) {
      display: none;
    }
  }
  &__social-col {
  	display: flex;
  	justify-content: space-between;
  	align-items: center;
  }
  .copy {
    margin-top: 24px;
    color: #7D7D7D;
    @media (max-width: 767px) {
      font-size: 12px;
      text-align: center;
    }
  }
  &__title {
    font-weight: 700;
    margin-bottom: 17px;
    @media (max-width: 767px) {
      margin-bottom: 0;
    }
  }
  .socials {
    margin-bottom: 0;
    @media (max-width: 767px) {
      align-items: center;
      justify-content: center;
      width: 100%;
    }
  }
  @media (max-width: 767px) {
    .links {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-bottom: 25px;
      .switcher-dropdown {
        margin-bottom: 0;
        margin: 0 15px;
        line-height: 20px;
        border-bottom: 1px solid #333;
      }
    }
    .foot-tab-head {
      position: relative;
      border-bottom: 1px solid #CCCCCC;
      padding: 16px 25px 10px 0;
      &:after {
        content: "";
        display: block;
        position: absolute;
        right: 10px;
        top: 50%;
        width: 8px;
        height: 8px;
        border-bottom: 2px solid #61616A;
        border-right: 2px solid #61616A;
        transform: translateY(-50%) rotate(45deg);
        transition: all .3s ease;
      }
      &.active {
        &:after {
          top: 60%;
          transform: translateY(-50%) rotate(-135deg);
        }
      }
    }
    .foot-tab-body {
      display: none;
      &.active {
        display: block;
      }
    }
  }
  .subscribe {
    input {
      &.parsley-error {
        border: 1px solid red;
      }
    }
    @media (max-width: 500px) {
      display: block;
      .form-item {
        width: 100%;
        // margin-bottom: 15px;
      }
      .form-action {
        width: 100%;
      }
    }
  }
}
