body.hidden {
  overflow: hidden;
  .popup {
    display: block;
  }
}
.popup.show_popup {
  visibility: visible;
  opacity: 1;
}
.popup {
  position: fixed;
  z-index: 9999;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(#CCCCCC, 0.8);
  opacity: 0;
  display: none;
  visibility: hidden;
  transition: 0.5s;
  .close  {
    position: absolute;
    right: 20px;
    top: 20px;
    width: 28px;
    height: 28px;
    cursor: pointer;
    border: 1px solid #CCCCCC;
    transition: opacity .3s ease;
    &:before, &:after {
      content: "";
      display: block;
      position: absolute;
      width: 2px;
      height: 18px;
      background-color: #333;
      top: 50%;
      left: 12px;
      transform: translate(-50%, -50%);
    }
    &:before {
      transform: translate(0, -50%) rotate(45deg);
    }
    &:after {
      transform: translate(0, -50%) rotate(-45deg);
    }
    &:hover {
      opacity: 0.8;
    }
    @media (max-width: 1080px) {
      top: 15px;
      right: 15px;
    }
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
  &__col {
    width: 50%;
    align-self: stretch;
    &:last-child {
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
      @media (max-width: 767px) {
        width: 40%;
      }
    }
    &:first-child {
      @media (max-width: 767px) {
        width: 60%;
      }
    }
  }
  label {
    text-align: left;
    margin-bottom: 8px;
  }
  &__content {
    position: absolute;
    // top: 20%;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    width: 750px;
    max-width: 100%;
    min-height: 290px;
    // display: flex;
    // align-items: center;
    // justify-content: center;
    // flex-direction: column;
    padding: 40px 58px;
    background: #fff;
    text-align: center;
  }
  &__title {
    text-transform: uppercase;
    font-size: 24px;
    font-weight: 700;
    margin-bottom: 20px;
  }
  &__registr {
    text-align: center;
    margin-top: 8px;
    margin-bottom: 40px;
    p, a {
      font-size: 12px;
      display: inline-block;
      color: #7D7D7D;
    }
    a {
      transition: .3 ease;
      &:hover {
        text-decoration: none;
      }
    }
  }
  &_subscribe-success, &_order-success, &_uncorrect-sertificate,&_uncorrect-sertificate, &_send-code, &_pass-message {
    .popup__content {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
    .popup__title {
      color: $pink;
      margin-bottom: 40px;
    }
  }
  &_order-success {
    .popup__title {
      color: $pink;
      margin-bottom: 20px;
    }
    p {
      margin-bottom: 32px;
    }
  }
  &_uncorrect-sertificate, &_send-code, &_pass-message {
    .popup__title {
      text-transform: uppercase;
      color: #333333;
      margin-bottom: 20px;
    }
    p {
      margin-bottom: 40px;
    }
  }
  &_change-adress {
    .popup__title {
      margin-bottom: 10px;
    }
    .popup__content {
      @media (max-width: 767px) {
        padding: 30px 15px 20px;
      }
    }
  }
  &_password-recovery {
    .popup__content {
      padding: 0;
      width: 950px;
      @media (max-width: 1080px) {
        width: 100%;
      }
    }
    .popup__col:first-child {
      padding: 40px 35px;
      @media (max-width: 1080px) {
        padding: 30px 20px;
      }
      @media (max-width: 768px) {
        padding: 20px 10px;
      }
    }
    .close {
      border-color: #333;
      &:hover {
        opacity: 0.6;
      }
    }
    .form-item.hiiden {
      display: none;
    }
  }
  &_choose, &_code {
    .popup__inner {
      min-height: 290px;
    }
    .popup__col {
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
  }
  &_code {
    .form-action {
      margin-top: 40px;
    }
    .form-item {
      margin-bottom: 8px;
    }
  }
  &_log-in {

  }
  &_basket {
    .ov-x-h {
      max-height: 420px;
      padding: 0 30px;
      &::-webkit-scrollbar-track {
        background-color: #EBEBEB;
      }
      &::-webkit-scrollbar {
        width: 4px;
        height: 4px;
        background-color: #EBEBEB;
      }
      &::-webkit-scrollbar-thumb {
        background-color: #333333;
        width: 4px;
        height: 4px;
      }
      @media (max-width: 1600px) {
        max-height: 250px;
      }
      @media (max-width: 1400px) {
        max-height: 315px;
      }
      @media (max-height: 795px) {
        max-height: 315px;
      }
      @media (max-width: 767px) {
        max-height: 365px;
        padding: 0 10px;
      }
    }
    .popup__title {
      margin-bottom: 20px;
      @media (max-width: 767px) {
        margin-bottom: 10px;
      }
    }
    .popup__content {
      // top: 50%;
      width: 950px;
      height: 970px;
      // transform: translateX(-50%) translateY(-50%);
      @media (max-width: 1600px) {
        height: 750px;
      }
      @media (max-width: 1400px) {
        height: 530px;
      }
      @media (max-height: 795px) {
        height: 550px;
      }
      @media (max-width: 1080px) {
        width: 100%;
        padding: 30px 20px;
      }
      @media (max-width: 767px) {
        height: 100%;
        padding: 30px 10px;
      }
    }
    .popup__row-bottom {
      padding: 20px 0;
      display: flex;
      justify-content: space-between;
      align-items: center;
      border-top: 1px solid #EBEBEB;
      h4 {
        margin-bottom: 0;
      }
      &:first-child {
        padding: 20px;
      }
      @media (max-width: 1600px) {
        padding: 10px 0;
        &:first-child {
          padding: 10px;
        }
      }
      // @media (max-width: 480px) {
      //   flex-wrap: wrap;
      //   &:last-child {
      //     justify-content: center;
      //   }
      // }
    }
    .goods {
      margin-bottom: 0;
    }
    .goods .item__img {
      padding-bottom: 130%;
    }
    .goods .item__title {
      font-size: 14px;
      line-height: 20px;
      font-weight: normal;
      text-align: left;
      margin-top: 8px;
    }
    .goods .item__label{
      justify-content: flex-end;
    }
    .popup__related-goods {
      h4 {
        font-weight: normal;
        margin-bottom: 20px;
      }
      @media (max-width: 1400px) {
        display: none;
      }
      @media (max-height: 795px) {
        display: none;

      }
    }
  }
}

.link-outer {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  a {
    font-size: 12px;
    color: $pink;
    text-decoration: none;
    border-bottom: 1px solid $pink;
    padding-bottom: 3px;
    display: inline-block;
    transition: .3s;
    &:hover {
      opacity: 0.6;
    }
  }
}

.basket {
  &__inner {
    padding-bottom: 20px;
  }
  &__bottom {
    margin: 20px 0 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .item {
      font-size: 14px;
      line-height: 20px;
      margin-bottom: 20px;
      padding-top: 20px;
      border-top: 1px solid #EBEBEB;
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
    h4 {
      margin-bottom: 0;
      font-size: inherit;
      line-height: 20px;
      text-transform: none;
    }
    &__row-outer {
      margin-top: 22px;
      @media (max-width: 680px) {
        margin-top: 10px;
      }
    }
    &__row-name {
      margin-bottom: 6px;
    }
    &__img {
      min-height: 156px;
      min-width: 120px;
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
    }
    &__info {
      width: calc(100% - 140px);
      display: flex;
      justify-content: space-between;
      @media (max-width: 680px) {
        flex-wrap: wrap;
      }
    }
    &__col {
      width: 50%;
      align-self: stretch;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      &:first-child {
        text-align: left;
      }
      &:last-child {
        text-align: right;
      }
      @media (max-width: 680px) {
        width: 100%;
        &:last-child {
          text-align: left;
          display: flex;
        }
      }
    }
    .sum {
      @media (max-width: 680px) {
        order: 2;
        margin-top: 10px;
      }
    }
    &__remove {
      display: inline-block;
      @media (max-width: 680px) {
        order: 3;
        margin-top: 10px;
      }
    }
    &__remove-link {
      cursor: pointer;
      transition: opacity .3s ease;
      &:hover {
        opacity: 0.8;
      }
    }
  }
  .quantity {
    justify-content: flex-end;
    margin-bottom: 0;
    @media (max-width: 680px) {
      justify-content: flex-start;
      margin-top: 10px;
    }
  }
  .sizes, .colors {
    margin-bottom: 0;
    &__item {
      margin-bottom: 5px;
    }
  }
}

.autr {
  display: flex;
  justify-content: center;
  align-items: center;
  &__item {
    border: 2px solid #EBEBEB;
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    transition: .3s;
    cursor: pointer;
    &:hover {
      opacity: 0.6;
    }

  }
  span {
    padding: 0 25px;
    display: inline-block;
    @media (max-width: 767px) {
      padding: 0 10px;
    }
  }
}
