.product-card {
  .goods_related {
    margin-bottom: 80px;
    &:last-child {
      margin-bottom: 0;
    }
    .price {
      color: #333;
        &_prev {
      color: #9E9E9E;
      }
    }
  }
  h4 {
    font-weight: 700;
  }
  &__top {
    margin-bottom: 80px;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    @media (max-width: 1080px) {
      display: block;
    }
  }
  &__img {
    width: 48%;
    display: flex;
    justify-content: space-between;
    @media (max-width: 1080px) {
      width: 100%;
      margin-bottom: 40px;
      flex-direction: column-reverse;
    }
    .slider {
      &__item {
        position: relative;
      }
      &__img {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        // max-height: 730px;
        @media (max-width: 1080px) {
          background-position: top center;
        }
      }

      .slick-list {
        margin: -10px 0;
      }

      .slick-slide {
        margin:10px 0;
        @media (max-width: 1080px) {
          img {
            margin: 0 auto;
          }
        }
      }

    }
    .slider-for {
      width: 70%;
      margin-left: 20px;
      @media (max-width: 1080px) {
        width: 100%;
        margin-left: 0;
        margin-bottom: 20px;
      }
      .slider__item {
        min-height: 721px;
        @media (max-width: 768px) {
          min-height: 600px;
        }
        @media (max-width: 480px) {
          min-height: 480px;
        }
        @media (max-width: 380px) {
          min-height: 400px;
        }
      }
    }
    .slider-nav {
      width: 30%;
      overflow-x: hidden;
      @media (max-width: 1080px) {
        width: 100%;
      }
      .slider__item {
        min-height: 168px;
        min-width: 168px;
        @media (max-width: 380px) {
          min-height: 140px;
          min-width: 140px;
        }
      }
      .slick-list {
        overflow-y: hidden;
        max-height: 730px;
        padding: 0 !important;
      }
    }
  }
  &__info {
    width: 52%;
    padding-left: 30px;
    @media (max-width: 1080px) {
      width: 100%;
      padding-left: 0;
    }
  }
  &__title {
    margin-bottom: 8px;
    padding-right: 20px;
  }
  &__sizes-link {
    font-size: 12px;
    color: #9C9C9C;
    transition: .3s ;
    margin-bottom: 10px;
    display: inline-block;
    &:hover {
      text-decoration: none;
    }
  }
  &__sizes-choose {
    display: inline-block;
    color: $pink;
    font-size: 12px;
    vertical-align: middle;
    padding-right: 20px;
    position: relative;
    margin-bottom: 23px;
    span {
      display: inline-block;
      vertical-align: middle;
      margin-left: 10px;
    }
    &:after {
        content: "";
        display: block;
        position: absolute;
        right: 10px;
        top: 50%;
        width: 5px;
        height: 5px;
        border-top: 2px solid $pink;
        border-right: 2px solid $pink;
        transform: translateY(-50%) translateX(0) rotate(45deg);
        transition: all .3s ease;
    }
    &:hover {
      &:after {
        transform: translateY(-50%) translateX(3px) rotate(45deg);
      }
    }
  }
  &__row-name {
    font-weight: 700;
    font-size: 14px;
    margin-bottom: 8px;
    span {
      font-weight: 400;
      margin-left: 5px;
      display: inline-block;
    }
  }
  &__btns {
    margin-bottom: 40px;
    .btn {
      margin-right: 30px;
      &:last-child {
        margin-right: 0;
      }
      @media (max-width: 600px) {
        margin-top: 20px;
      }
    }
  }
  &__desc {
    .product-card__row-name {
      margin-bottom: 20px;
    }
    p {
      margin-bottom: 20px;
      font-size: 14px;
    }
  }
  &__row {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    @media (max-width: 600px) {
      display: block;
    }
    &_top {
      justify-content: space-between;
      .product-card__col:last-child {
        text-align: right;
        @media (max-width: 600px) {
          text-align: left;
        }
      }
    }
  }
  &__col {
    width: 50%;
    @media (max-width: 600px) {
        width: 100%;
        margin-bottom: 20px;
    }
  }
  &__icons {
    margin-top: 20px;
    margin-bottom: 10px;
    img {
      margin-right: 15px;
    }
  }
  &__price-outer {
    margin-bottom: 20px;
  }
  .price {
    color: $pink;
    display: inline-block;
    margin-right: 15px;
    &:last-child {
      margin-right: 0;
    }
    &_prev {
      color: #7D7D7D;
    }
  }
}
.quantity {
  display: flex;
  justify-content: flex-start;
  -webkit-align-items: center;
  align-items: center;
  margin-bottom: 20px;
  //     width: auto;
  // font-weight: 500;
  input:not([type="checkbox"]):not([type="radio"]), textarea{
    display: inline-block;
    width: auto;
    max-width: 40px;
    text-align: center;
    border: none;
    font-size: 14px;
    line-height: 20px;
  }
  button {
    background-color: transparent;
    border: 1px solid #CCCCCC;
    color: #CCC;
    height: 28px;
    width: 28px;
    position: relative;
    cursor: pointer;
    &:after {
      content: "";
      position: absolute;
      display: block;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      width: 14px;
      height: 1px;
      background-color: #CCC;
    }
  }
  &__right-plus {
    &:before {
      content: "";
      position: absolute;
      display: block;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      width: 1px;
      height: 14px;
      background-color: #CCC;
    }
  }
}

.colors {
  display: flex;
  justify-content: flex-start;
  -webkit-align-items: center;
  align-items: center;
  margin-bottom: 20px;
  label {
    font-weight: 300;
  }
  input{
    position: absolute;
    z-index: -1;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
  }
  &__item {
    position: relative;
    display: flex;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-align-items: center;
    align-items: center;
    margin: 0 4px;
    label{
      padding: 8px;
      line-height: 1;
      color: #A2A2A2;
      transition: all .3s ease;
      position: relative;
      border-radius: 50%;
      &:before{
        content: "";
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 22px;
        height: 22px;
        border: 1px solid #333;
        border-radius: 50%;
        opacity: 0;
        transition: all .3s ease;
      }
      &:hover{
        &:before{
          opacity: 1;
        }
      }
    }
  }
  input + label {
    position: relative;
    cursor: pointer;
  }
  input:checked + label:before{
    opacity: 1;
  }
  input:focus + label:before{
    opacity: 1;
  }

}


.art {
  color: #7D7D7D;
  font-size: 14px;
  line-height: 20px;
  margin-bottom: 20px;
}
.sizes {
  margin-bottom: 20px;
  label {
    font-weight: 300;
  }
  input{
    position: absolute;
    z-index: -1;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
  }
  &__item {
    position: relative;
    display: inline-block;
    margin-right: 8px;
    min-width: 26px;
    &:first-child{
      label{
        border-left: 1px solid #C9C9C9;
      }
    }
    &:last-child {
      margin-right: 0;
    }
  }
  label {
    padding: 3px;
    font-size: 14px;
    line-height: 20px;
    color: 7D7D7D;
    transition: all .3s ease;
    border: 1px solid #CCCCCC;
    display: inline-block;
    &:hover{
      background-color: #333333;
      color: #FFF;
      border-color: #7D7D7D;
    }
  }

  input + label {
    position: relative;
    cursor: pointer;
    width: 100%;
    height: 100%;
    text-align: center;
  }
  input:checked + label {
    background-color: #333333;
    color: #FFF;
    border-color: #333333;
  }

  input:focus + label {
    background-color: #333333;
    color: #FFF;
    border-color: #333333;
  }
}

.row-val {
  span {
    margin-left: 3px;
    display: inline-block;
  }
}
