.callback {
  margin-bottom: 40px;
  .form-item_textarea textarea {
    height: 140px;
  }
  .send-form__bottom {
    margin-top: 20px;

    justify-content: center;
    .btn , .form-action {
      margin: 0 10px;
    }
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    @media (max-width: 767px) {
      flex-wrap: wrap;
    }
  }
  &__col {
    align-self: stretch;
    &:first-child {
      width: 43%;
      @media (max-width: 767px) {
        width: 100%;
        margin-bottom: 40px;
      }
    }
    &:last-child {
      width: 55%;
      @media (max-width: 767px) {
        width: 100%;
      }
    }
  }
  &__info {
    padding: 20px;
    background-color: #F8F8F8;
    border: 1px solid #CCCCCC;
    font-size: 16px;
    line-height: 20px;
    height: 100%;
  }
  &__info-title {
    color: $pink;
    margin-bottom: 40px;
  }
  &__info-links {
    margin: 40px 0;
    a {
      text-decoration: none;
      transition: .3s;
      &:hover {
        opacity: 0.7;
      }
    }
  }
  &__info-desc {
    max-width: 420px;
  }
}
