.search-without-results-page {
  .page-title {
    text-align: center;
  }
}


.search-results {
  .category {
    margin-top: 40px;
  }
  &__title {
    text-align: center;
    color: $pink;
    margin: 40px 0;
  }
  &__message {
    text-align: center;
    color: $pink;
    font-size: 14px;
    line-height: 20px;
    margin-top: 40px;
    margin-bottom: 6px;
    span {
      margin: 0 3px;
    }
  }
  &__info {
    text-align: center;
    font-size: 20px;
    line-height: 32px;
    margin-bottom: 40px;
    span, a {
      font-weight: bold;
      margin: 0 3px;
    }
    a {
      text-decoration: none;
      transition: .3s;
      &:hover {
        opacity: 0.6;
      }
    }
  }
}
