.main-slider {
  //can be deleted if no need arrorw space
  &__item {
    height: calc(100vh - 153px);
    position: relative;
    @media (max-width: 1080px) {
      height: 600px;
    }
    .slider-caption {
      position: absolute;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
      max-width: 450px;
      padding-right: 50px;
      color: #fff;
      @media (max-width: 767px) {
        transform: none;
        left: 0;
        top: auto;
        bottom: 8px;
        text-align: center;
        max-width: 100%;
        padding: 0 8px;
      }
    }.img {
      display: block;
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
    }
  }
  .title {
    font-size: 35px;
    line-height: 44px;
    margin-bottom: 24px;
  }
  .link {
    display: inline-block;
    padding: 14px 24px;
    text-transform: uppercase;
  }
  .desc {
    margin-bottom: 40px;
  }
  .slick-slider {
    @media (max-width: 767px) {
      overflow: hidden;
    }
  }
  // .slick-arrow {
  //   position: absolute;
  //   z-index: 50;
  //   top: 50%;
  //   transform: translateY(-50%);
  //   width: 40px;
  //   height: 40px;
  //   background: transparent;
  //   cursor: pointer;
  //   display: flex;
  //   align-items: center;
  //   justify-content: center;
  //   transition: opacity 0.5s;
  //   &.slick-disabled  {
  //     opacity: 0.1;
  //     cursor: default;
  //   }
  //   &.slick-prev  {
  //     left: -50px;
  //   }
  //   &.slick-next {
  //     right: -50px;
  //   }
  //   .icon {
  //     height: 60%;
  //     fill: #000;
  //   }
  // }
  .slick-dots {
    position: absolute;
    top: 0;
    left: 20px;
    width: auto;
    height: 100%;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    @media (max-width: 767px) {
      width: 100%;
      height: auto;
      flex-direction: row;
      top: 12px;
    }
    li{
      display: inline-block;
      width: 10px;
      height: 10px;
      border-radius: 50%;
      background-color: #FFF;
      margin: 15px;
      cursor: pointer;
      position: relative;
      transition: opacity .3s ease;
      @media (max-width: 767px) {
        margin: 12px;
      }
      &:hover {
        opacity: 0.6;
      }
      &.slick-active {
        &:before  {
          opacity: 1;
        }
      }
      &:before {
        content: "";
        display: inline-block;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 34px;
        height: 34px;
        border: 2px solid #fff;
        border-radius: 50%;
        opacity: 0;
        transition: opacity .3s ease;
      }
      button {
        display: none;
      }
    }
  }
}


.news-slider {
  padding-bottom: 40px;
  &__item {
    outline: none !important;
  }
  &__item-inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @media (max-width: 767px) {
      display: block;
    }
  }
  &__img {
    width: 53%;
    min-height: 460px;
    height: 100%;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    outline: none !important;
    display: inline-block;
    @media (max-width: 767px) {
      width: 100%;
      min-height: 400px;
    }
  }
  &__info {
    width: 47%;
    padding-left: 20px;
    text-align: center;
    @media (max-width: 767px) {
      width: 100%;
      padding-left: 0;
    }
  }
  &__desc {
    font-size: 20px;
    margin-bottom: 40px;
  }
  .slick-dots {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: auto;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    li{
      display: inline-block;
      width: 10px;
      height: 10px;
      border-radius: 50%;
      border: 1px solid #333;
      margin: 12px;
      cursor: pointer;
      position: relative;
      transition: opacity .3s ease;
      &:hover {
        opacity: 0.6;
      }
      &.slick-active {
        background-color: #333;
      }
      button {
        display: none;
      }
    }
  }
}
