.registration {
  margin-bottom: 40px;
  &__social {
    margin: 20px 0 40px;
    text-align: center;
    span {
      margin: 0 20px;
    }
  }
  &__social-item {
    text-decoration: none;
    width: 50px;
    height: 50px;
    border: 2px solid #EBEBEB;
    display: inline-block;
    padding: 10px;
    border-radius: 50%;
  }
  .page-title {
    text-align: center;
    margin-bottom: 20px;
  }
