.hero {
  margin-bottom: 23px;
}

.promo {
  margin-bottom: 30px;
  &__item {
    width: calc(100% / 3);
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-right: 10px;
    @media (max-width: 1200px) {
      width: 50%;
    }
    @media (max-width: 767px) {
      width: 100%;
      padding-right: 0;
      margin-bottom: 15px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &__icon {
    width: 20%;
    min-width: 90px;
    @media (max-width: 767px) {
      min-width: 120px;
    }
  }
  &__text {
    padding-left: 10px;
  }
}

.banner {
  width: 100%;
  height: auto;
  margin-bottom: 20px;
}

.about {
  margin-bottom: 40px;
  @media (max-width: 767px) {
    .container {
      padding: 0;
    }
  }
}

.about__row {
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
}
@media (max-width: 768px) {
  .about__row {
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
  }
}
.about__col {
  -webkit-align-self: stretch;
  -ms-flex-item-align: stretch;
      align-self: stretch;
  width: 50%;
}
@media (max-width: 1080px) {
  .about__col {
    width: 100%;
    margin-bottom: 20px;
  }
}
@media (max-width: 767px) {
    .about__col {
      &:first-child {
        padding: 0 15px;
      }
    }
}
.about__img {
  min-height: 500px;
  width: 100%;
  height: 100%;
  background-position: top center;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (max-width: 768px) {
  .about__img {
    min-height: 400px;
  }
}
.about h4 {
  margin-top: 15px;
  margin-bottom: 25px;
}

.about .ov-h {
  height: 500px;
}
.about .ov-a {
  padding-right: 60px;
}

@media (max-width: 768px) {
  .about .ov-h {
    height: 300px;
  }
}
.about .ov-a::-webkit-scrollbar-track {
  background-color: #EBEBEB;
}
.about .ov-a::-webkit-scrollbar {
  width: 10px;
  height: 10px;
  background-color: #EBEBEB;
}
.about .ov-a::-webkit-scrollbar-thumb {
  background-color: $pink;
  width: 10px;
  height: 10px;
}

.shop-items {
  margin-bottom: 20px;
  &__item-outer {
    padding: 0 15px;
    width: 33%;
    min-height: 365px;
    align-self: stretch;
    @media (max-width: 1200px) {
      padding: 0 5px;
    }
    @media (max-width: 767px) {
      width: 100%;
      padding: 0;
      margin-bottom: 15px;
    }
  }
  .row {
    @media (max-width: 1200px) {
      margin-right: -5px;
      margin-left: -5px;
    }
    @media (max-width: 767px) {
      margin: 0;
    }
  }
  &__item {
    width: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    flex-wrap: nowrap;
    text-align: center;
    padding: 0 20px;
    position: relative;
    min-height: 630px;
    @media (max-width: 1200px) {
      min-height: 560px;
    }
    @media (max-width: 767px) {
      background-position: top center;
    }
    &:after {
      content: "";
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      z-index: 1;
      background-color: rgba(51, 51, 51, 0.1);
    }
  }
  &__title {
    color: #FFF;
    text-transform: uppercase;
    font-size: 24px;
    font-weight: 700;
    line-height: 34px;
    margin-bottom: 24px;
    position: relative;
    z-index: 2;
  }
  .btn {
    margin-bottom: 40px;
    position: relative;
    z-index: 2;
  }
}

.news {
  margin-bottom: 60px;
}

.subscribe-block {
  margin-bottom: 80px;
  text-align: center;
  &__title {
    margin-bottom: 24px;
  }
  .subscribe {
    max-width: 670px;
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    input {
      background-color: #EBEBEB;
      padding: 14px 24px;
      border: 1px solid #EBEBEB;
      height: 48px;
      border: none;
      &::placeholder {
        color: #9E9E9E;
      }
      &.parsley-error {
        background-color: #F2DEDE;
      }
    }
    .form-item {
      padding-bottom: 0;
      padding-top: 0;
      width: 100%;
      margin-bottom: 0;
    }
    .form-action {
      margin-top: 0;
    }
    .btn {
      min-height: 48px;
    }
  }
}

.goods {
  margin-bottom: 80px;
  @media (max-width: 1080px) {
    .goods__item-outer {
      flex: 0 0 25%;
      max-width: 25%;
    }
  }
  @media (max-width: 767px) {
    margin-bottom: 60px;
    .goods__item-outer {
      flex: 0 0 50%;
      max-width: 50%;
      margin-bottom: 20px;
    }
  }
  @media (max-width: 480px) {
    .goods__item-outer {
      flex: 0 0 100%;
      max-width: 100%;
    }
  }
  &_popular {
    .goods__item-outer {
      flex: 0 0 33%;
      max-width: 33%;
    }
    @media (max-width: 767px) {
      .goods__item-outer {
        flex: 0 0 50%;
        max-width: 50%;
        &:last-child {
          display: none;
        }
      }
    }
    @media (max-width: 480px) {
      .goods__item-outer {
        flex: 0 0 100%;
        max-width: 100%;
      }
    }
  }
  &_related {
    h3 {
      text-align: center;
    }
    @media (max-width: 1080px) {
      .goods__item-outer {
        flex: 0 0 25%;
        max-width: 25%;
        &:last-child {
          display: none;
        }
      }
    }
    @media (max-width: 767px) {
      margin-bottom: 60px;
      .goods__item-outer {
        flex: 0 0 50%;
        max-width: 50%;
        margin-bottom: 20px;
      }
    }
    @media (max-width: 480px) {
      .goods__item-outer {
        flex: 0 0 100%;
        max-width: 100%;
      }
    }

  }
  &_sm {
    .goods__item-outer {
      flex: 0 0 33%;
      max-width: 33%;
    }
  }
  &__inner {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -10px;
  }
  &__item-outer {
    flex: 0 0 20%;
    max-width: 20%;
    padding: 0 10px;
    margin-bottom: 20px;
  }
  .item {
    position: relative;
    &__title {
      font-weight: 700;
      font-size: 20px;
      line-height: 32px;
      display: block;
      text-decoration: none;
      transition: color .3s ease;
      &:hover {
        color: $pink;
      }
    }
    &__img {
      padding-bottom: 155%;
      display: block;
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
      transition: background .2s linear;
      // &:hover {
      //   opacity: 0.8;
      // }
      @media (max-width: 480px) {
        padding-bottom: 130%;
      }
    }
    &__label {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      z-index: 2;
      padding: 16px 42px 16px 16px;
      display: block;
      min-height: 62px;
      .wish-btn {
        position: absolute;
        right: 16px;
        top: 50%;
        transform: translateY(-50%);
      }
    }
    &__info {
      margin-top: 8px;
    }
  }
}
