.news {
  &_related {
    @media (max-width: 767px) {
      .news__item-outer {
        &:last-child {
          display: none;
        }
      }
    }
  }
  .page-title {
    text-align: center;
  }
  &__outer {
    display: flex;
    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    margin: 0 -20px;
    @media (max-width: 1080px) {
      margin: 0 -10px;
    }
  }
  &__item {
    width: 100%;
    height: 100%;
    -webkit-align-self: stretch;
    align-self: stretch;
    p {
      max-height: 63px;
      overflow: hidden;
    }
  }
  &__item-outer {
    flex: 0 0 33%;
    max-width: 33%;
    padding: 0 20px;
    margin-bottom: 40px;
    @media (max-width: 1080px) {
      padding: 0 10px;
    }
    @media (max-width: 767px) {
      flex: 0 0 50%;
      max-width: 50%;
    }
    @media (max-width: 550px) {
      flex: 0 0 100%;
      max-width: 100%;
    }
  }
  &__img {
    display: inline-block;
    width: 100%;
    padding-bottom: 50%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    margin-bottom: 8px;
  }
  &__inner {
    span {
      font-size: 12px;
      line-height: 18px;
      display: block;
      margin-bottom: 8px;
    }
  }
  h4 {
    margin-bottom: 20px;
    a {
      display: inline-block;
      text-decoration: none;
      transition: color .3s ease;
      &:hover {
        color: $pink;
      }
    }
  }
}

.text-page {
  margin-bottom: 60px;
  .page-title {
    margin-bottom: 40px;
  }
  .data {
    margin-bottom: 40px;
    display: inline-block;
  }
  p {
    text-align: justify;
    &:after {
      content: "";
      clear: both;
      display: table;
    }
  }

}


.one-discount {
  margin-bottom: 40px;
  &__inner {
    border: 4px solid #EBEBEB;
    padding: 40px;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    @media (max-width: 767px) {
      flex-wrap: wrap;
    }
  }
  &__col {
    &:first-child {
      width: 40%;
      @media (max-width: 767px) {
        width: 100%;
        margin-bottom: 20px;
      }
    }
    &:last-child {
      width: 60%;
      padding: 0 20px;
      @media (max-width: 767px) {
        width: 100%;
        padding: 0;
      }
    }
  }
  .page-title {
    text-align: center;
  }
  &__info {
    color: $pink;
  }
  h4 {
    margin-bottom: 40px;
  }
  p {
    font-size: 14px;
    line-height: 20px;
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 40px;
    }
  }
  strong {
    margin: 0;
  }
}
