.policy {
  margin-bottom: 40px;
  padding: 0 40px;
  border: 1px solid #CCCCCC;
  @media (max-width: 768px) {
    padding: 0 20px;
  }
  @media (max-width: 480px) {
    padding: 0 10px;
  }
  &__inner {
    padding: 0 180px;
    @media (max-width: 1200px) {
      padding: 0 40px;
    }
    @media (max-width: 768px) {
      padding: 0 20px;
    }
    @media (max-width: 480px) {
      padding: 0 10px;
    }
  }
  .ov-h {
    height: 525px;
    padding: 40px 0;
  }
  .ov-a {
    &::-webkit-scrollbar-track {
      background-color: #EBEBEB;
    }
    &::-webkit-scrollbar {
      width: 4px;
      height: 4px;
      background-color: #EBEBEB;
    }
    &::-webkit-scrollbar-thumb {
      background-color: #333333;
      width: 4px;
      height: 4px;
    }
  }
  p {
    margin-bottom: 20px;
  }
}
