.order {
  margin-bottom: 40px;

  .page-title {
    text-align: center;
  }
  h4 {
    padding: 0 20px;
    @media (max-width: 767px) {
      padding: 0 10px;
    }
  }
  &__row {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    @media (max-width: 1240px) {
      flex-wrap: wrap;
    }
  }
  &__col {
    width: calc(66.66% - 10px);
    flex-basic: calc(66.66% - 10px);
    &:last-child {
      width: calc(33.33% - 10px);
      flex-basic: calc(33.33% - 10px);
    }
    @media (max-width: 1240px) {
      width: 100%;
      flex-basic: 100%;
      &:last-child {
        margin-top: 30px;
        width: 100%;
        flex-basic: 100%;
      }
    }
  }

  .basket {
    padding: 20px;
    border: 1px solid #CCCCCC;

    &__item {
      margin-bottom: 10px;
      color: #7D7D7D;
    }

    &__inner {
      padding-bottom: 0;
    }

    &__title {
      text-align: center;
    }
    &__total {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-top: 20px;
      p {
        margin-bottom: 0;
      }
    }
    label {
      font-size: 14px;
      line-height: 20px;

    }
    textarea {
      height: 80px;
    }
    .quantity {
      justify-content: flex-start;
    }
    .item {
      margin-bottom: 0;
      padding: 20px 0;
      border-top: none;
      border-bottom: 1px solid #CCCCCC;
      margin-bottom: 10px;
      @media (max-width: 600px) {
        display: block;
      }
      h4 {
        padding: 0;
      }
      &:last-child {
        border-bottom: none;
      }
      &__info {
        display: block;
        @media (max-width: 600px) {
          width: 100%;
        }
      }
      &__row-outer {
        margin-top: 15px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
      }
      &__img {
        height: 180px;
        width: 150px;
        margin-right: 20px;
        @media (max-width: 600px) {
          margin-right: 0;
          margin-bottom: 10px;
        }
      }
      .price {
        font-size: 14px;
        margin-right: 16px;
        &:last-child {
          margin-right: 0;
        }
      }
      .colors, .sizes {
        margin-right: 8px;
      }
    }
  }
  .link {
    display: inline-block;
    text-decoration: none;
    border-bottom: 1px solid $pink;
    margin-right: 20px;
    color: $pink;
    padding-bottom: 3px;
    transition: all .3s ease;
    font-size: 12px;
    &:hover {
      opacity: 0.6;
    }
  }
  .map {
    height: 390px;
    margin-bottom: 20px;
  }

  .tab-self-pickup, .tab-courier {
    .send-form__col.tab-pane.active {
      padding-left: 20px;
      @media (max-width: 767px) {
        padding: 0;
        padding: 0 10px 0 10px;
      }
    }
  }
  .tab-pickup {
    .form-item_courier-com{
      display: none;
    }
  }
  .tab-courier {
    .form-item_courier-com{
      display: block;
    }
    .map {
      display: none;
    }
  }
  .tab-self-pickup {
    .map {
      display: none;
    }
    .form-item_courier-com{
      display: none;
    }
  }
  &_client {
    .tab-courier {
      .form-item_paymenttype {
        display: none;
      }
      .send-form__link-outer_message {
        .form-item.textarea.hidden {
          display: block;
        }
      }
      .send-form__link-outer_bonuse {
        display: none;
      }
    }
  }
  .page-form__head {
    @media (max-width: 767px) {
      padding: 20px 10px;
    }
  }
  .page-form_tabs {
    .page-form__head {
      @media (max-width: 600px) {
        padding: 0 10px;
      }
    }
    .send-form__col {
      margin-bottom: 0;
    }
  }
  .basket {
    .item {
      position: relative;
      h4{
        padding-right: 40px;
      }
      &__remove {
        position: absolute;
        top: 16px;
        right: 0;

      }
    }
  }
}

.bonus {
  color: #7D7D7D;
  font-size: 12px;
  line-height: 18px;
  &__outer {
    margin: 10px 0 0;
   @media (max-width: 600px) {
      padding: 0 10px;
    }
  }
  &__item {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    position: relative;
    margin-bottom: 8px;
    &:after {
      content: "";
      display: block;
      position: absolute;
      border-bottom: 1px dashed #9C9C9C;
      width: 100%;
      bottom: 4px;
    }
  }
  .bonus__info, .bonus__sum {
    background-color: #FFF;
    position: relative;
    z-index: 3;
  }
  button {
    margin: 20px auto 10px;
  }
  &__bottom {
    text-align: center;
  }
  &__text {
    margin-bottom: 8px;
    span {
      margin: 0 3px;
    }
  }
  .send-form__link {
    margin-bottom: 0;
  }
}
