.account {
  padding: 40px 0;
  .page-title {
    text-align: center;
  }
  .card {
    margin: auto;
    padding: 40px 60px;
    background: #F8F8F8;
    border: 3px solid #EBEBEB;
    border-radius: 15px;
    max-width: 520px;
      @media (max-width: 768px) {
        padding: 40px 20px;
    }
    &__img {
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat;
      width: 400px;
      height: 175px;
      display: flex;
      justify-content: center;
      align-items: flex-end;
      @media (max-width: 1080px) {
        width: 100%;
      }
      @media (max-width: 768px) {
        height: 130px;
      }
    }
    &__number {
      padding: 8px 17px;
      background: #F8F8F8;
    }
    &__head {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 20px;
    }
    &__title {
      text-transform: uppercase;
      color: $pink;
      font-size: 16px;
      line-height: 20px;
    }
    .link {
      font-size: 12px;
      color: #7D7D7D;
      text-decoration: none;
      border-bottom: 1px solid #7D7D7D;
    }
  }
  &__row {
    display: flex;
    justify-content: space-around;
    align-items: flex-start;
    @media (max-width: 1080px) {
      flex-wrap: wrap;
     }
  }
  &__col {
    &:first-child {
      width: calc(20% - 10px);
      @media (max-width: 1080px) {
        width: 100%;
      }
    }
    &:last-child {
      width: calc(80% - 10px);
      @media (max-width: 1080px) {
        width: 100%;
      }
    }
  }
  &__category {
    li {
      margin-bottom: 8px;
    }
    a {
      font-size: 14px;
      line-height: 20px;
      text-decoration: none;
      transition: .3s;
      &.active {
        color: $pink;
      }
      &:hover {
        opacity: 0.6;
      }
    }
  }
  &__results {
    margin-bottom: 20px;
    border-bottom: 1px solid #7D7D7D;
    display: flex;
    justify-content: flex-end;
  }
  &__result-info {
    font-size: 14px;
    color: #7D7D7D;
    margin-bottom: 8px;
    span {
      margin-right: 3px;
    }
  }
  .goods {
    &__item-outer {
      flex: 0 0 25%;
      max-width: 25%;
      @media (max-width: 767px) {
        flex: 0 0 50%;
        max-width: 50%;
      }
      @media (max-width: 480px) {
        flex: 0 0 100%;
        max-width: 100%;
      }
    }
  }
  .acordeon {
    .item {
      background-color: #F8F8F8;
      margin-bottom: 20px;
      &__head {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 16px 120px 16px 16px;
        margin-bottom: 0;
        @media (max-width: 767px) {
          display: block;
          padding: 16px 60px 16px 16px;
        }
      }
      &__info {
        @media (max-width: 767px) {
          margin-bottom: 10px;
          &:last-child {
            margin-bottom: 0;
          }
        }
        span {
          margin: 0 3px;
        }
      }
      &__result {
        span {
          margin: 0 3px;
        }
      }
      &__bottom {
        padding: 40px 0;
        border-top: 1px dashed #9C9C9C;
        border-bottom: 1px dashed #9C9C9C;

        h6 {
          text-align: center;
        }
      }
      &__bottom-info {
        padding: 20px 0;
        color: #7D7D7D;
        max-width: 750px;
        p {
          font-size: 12px;
          line-height: 18px;
        }
      }
      .quantity input:not([type="checkbox"]):not([type="radio"]), .quantity textarea {
        background-color: transparent;
      }
    }
  }
  .basket {
    .item {
      &__col:last-child {
        justify-content: flex-start;
        align-items: flex-end;
        @media (max-width: 680px) {
          align-items: flex-start;
        }
      }
      &__img {
        @media (max-width: 680px) {
          min-height: 120px;
          min-width: 90px;
        }
      }
      &__info {
        @media (max-width: 680px) {
          width: calc(100% - 100px);
        }
      }
    }
    .btn {
      max-width: 172px;
      margin-top: 25px;
      padding: 7px 20px;
      @media (max-width: 680px) {
        height: auto;
      }
    }
  }
}

.bonus-history {
    @media (max-width: 680px) {
      white-space: normal;
      overflow-x: scroll;
    }
  &__row {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding: 11px 0;
    font-size: 12px;
    @media (max-width: 680px) {
      min-width: 900px;
    }
    &:nth-child(2n) {
      background-color: #F8F8F8;
    }
    &:first-child {
      color: #7D7D7D;
      font-size: 14px;
    }
  }
  &__col {
    width: calc(100% / 6);
    text-align: center;
    align-self: stretch;
    padding: 5px 10px;
    span {
      margin: 0 3px;
    }
  }
}

.order-steps {
  display: flex;
  justify-content: space-between;
  align-items: center;
  @media (max-width: 767px) {
    flex-wrap: wrap;
  }
  &__title {
    max-width: 80px;
    font-size: 12px;
    line-height: 18px;
    text-align: center;
    @media (max-width: 767px) {
      margin: auto;
    }
  }
  &__item {
   position: relative;
   padding-bottom: 48px;
   min-height: 82px;
   padding-left: 70px;
    @media (max-width: 1200px) {
      padding-left: 60px;
      // min-width: 140px;
   }
    @media (max-width: 1080px) {
      padding-left: 0;
    }

    @media (max-width: 767px) {
      min-width: 33.3%;
      text-align: center;
      margin-bottom: 10px;
    }
   &:before {
    content: "";
    display: block;
    position: absolute;
    background-color: #CCC;
    height: 4px;
    width: 35px;
    bottom: 19px;
    // left: -100%;
    left: 0;
    // @media (max-width: 1360px) {
    //   left: -90%;
    // }
    @media (max-width: 1080px) {
      content: none;
    }
   }
   &:first-child {
    padding-left: 0;
     &:before {
       content: none;
     }
   }
    &.active {
      color: #3D8D74;
      &:before {
        background-color: #75BCB1;
      }
      .order-steps__icon {
        border-color: #75BCB1;
        &:before {
          opacity: 1;
        }
      }
    }
  }
  &__icon {
    position: absolute;
    bottom: 0;
    right: 20px;
    width: 40px;
    height: 40px;
    border: 4px solid #CCCCCC;
    border-radius: 50%;
    @media (max-width: 767px) {
      right: auto;
      left: 50%;
      transform: translateX(-50%);
    }
    &:before {
      content: "";
      display: block;
      position: absolute;
      top: 46%;
      left: 50%;
      transform: translate(-50%, -50%) rotate(45deg);
      opacity: 0;
      width: 7px;
      height: 15px;
      border-right: 1px solid #3D8D74;
      border-bottom: 1px solid #3D8D74;

    }
  }
}
